export default "import { memo, Fragment } from \"react\";\nimport { Helmet } from \"react-helmet\";\nimport { Header, Divider } from \"semantic-ui-react\";\nimport { Body } from \"../../Styles\";\nimport { Project } from \"../Project\";\nimport { workProjects } from \"../../../config\";\n\nfunction WorkExp(props) {\n\treturn (\n\t\t<Body>\n\t\t\t<Helmet>\n\t\t\t\t<title>My Work Experiences | {process.env.REACT_APP_TITLE}</title>\n\t\t\t</Helmet>\n\n\t\t\t<Header size=\"huge\" style={{ cursor: \"default\" }}>\n\t\t\t\tMy Work Experiences\n\t\t\t</Header>\n\n\t\t\t{(workProjects || [])\n\t\t\t\t.map(({ projectId, ...rest }) => ({ ...rest, path: `/workExp/${projectId}` }))\n\t\t\t\t.map((project, i) => (\n\t\t\t\t\t<Fragment key={i}>\n\t\t\t\t\t\t<Divider section />\n\t\t\t\t\t\t<Project detail={project} />\n\t\t\t\t\t</Fragment>\n\t\t\t\t))}\n\t\t</Body>\n\t);\n}\n\nconst memoComponent = memo(WorkExp, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as WorkExp };\n";