export default "import { useState } from \"react\";\nimport { useDispatch } from \"react-redux\";\nimport Lazyload from \"react-lazyload\";\nimport { changeImagePopupSource } from \"../../../../../redux/action\";\n\nfunction CustomMarkdownImage(props) {\n\tconst { src, style, ...rest } = props;\n\n\tconst [isHovered, setHovered] = useState(false);\n\tconst [queryString, setQueryString] = useState(\"\");\n\n\tconst dispatch = useDispatch();\n\n\treturn (\n\t\t<Lazyload once height=\"400px\" offset={400}>\n\t\t\t<picture>\n\t\t\t\t<source type=\"image/webp\" srcSet={encodeURIComponent(src.replace(/\\.(jpg|png|gif)/, \".webp\"))} />\n\t\t\t\t<img\n\t\t\t\t\t{...rest}\n\t\t\t\t\tsrc={`${src}${queryString}`}\n\t\t\t\t\talt={src.replace(/\\.(jpg|png|gif)/, \"\")}\n\t\t\t\t\tdraggable=\"false\"\n\t\t\t\t\tonContextMenu={(e) => e.preventDefault()}\n\t\t\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\t\t\tonClick={() =>\n\t\t\t\t\t\tdispatch(\n\t\t\t\t\t\t\tchangeImagePopupSource({\n\t\t\t\t\t\t\t\timages: [src],\n\t\t\t\t\t\t\t\tactiveIndex: 0,\n\t\t\t\t\t\t\t})\n\t\t\t\t\t\t)\n\t\t\t\t\t}\n\t\t\t\t\tonError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}\n\t\t\t\t\tstyle={{\n\t\t\t\t\t\t...style,\n\t\t\t\t\t\t...(isHovered && {\n\t\t\t\t\t\t\ttransform: \"scale(1.2)\",\n\t\t\t\t\t\t\tboxShadow: \"0 0 30px rgba(0, 0, 0, 1)\",\n\t\t\t\t\t\t\tzIndex: 1,\n\t\t\t\t\t\t}),\n\t\t\t\t\t}}\n\t\t\t\t/>\n\t\t\t</picture>\n\t\t</Lazyload>\n\t);\n}\n\nfunction CustomImageRenderer(props) {\n\treturn <CustomMarkdownImage {...props} />;\n}\nexport default CustomImageRenderer;\nexport { CustomImageRenderer };\n";