export default "import { memo, useState, useEffect } from \"react\";\nimport { useHistory } from \"react-router-dom\";\nimport { useSelector, useDispatch } from \"react-redux\";\nimport { useSwipeable } from \"react-swipeable\";\nimport styled from \"styled-components\";\nimport { LoadingIndicator } from \"./LoadingIndicator\";\nimport { NoSelect } from \"../Styles\";\nimport { changeImagePopupSource } from \"../../redux/action\";\n\nconst FullScreenGalleryWrapper = styled.div`\n\tdisplay: flex;\n\ttext-align: center;\n\tjustify-content: center;\n\talign-items: center;\n\n\tposition: fixed;\n\ttop: 0;\n\tleft: 0;\n\twidth: 100vw;\n\theight: 100vh;\n\tz-index: 1000;\n\tcursor: zoom-out;\n\ttransition: 500ms;\n\tbackground-color: rgba(0, 0, 0, 0.9);\n\topacity: ${({ show }) => (show ? 1 : 0)};\n\t${({ show }) => (show ? \"\" : \"pointer-events: none;\")};\n\n\timg {\n\t\twidth: 95vw;\n\t\theight: 95vh;\n\t\tobject-fit: contain;\n\t\ttransition: 500ms;\n\t\t${({ loaded }) => (loaded ? \"\" : \"filter: brightness(0.5);\")};\n\t}\n`;\n\nconst GalleryButtons = styled.div`\n\t.image-button {\n\t\tposition: fixed;\n\t\ttop: 0;\n\t\twidth: 33%;\n\t\theight: 100%;\n\t\tborder: none;\n\t\ttransition: 250ms;\n\t\tcursor: ${({ loaded }) => (loaded ? \"pointer\" : \"wait\")};\n\t\tbackground-color: transparent;\n\t}\n\n\t.previous-image-button {\n\t\tleft: 0;\n\t}\n\n\t.next-image-button {\n\t\tright: 0;\n\t}\n`;\n\nconst IndexAndCount = styled.span`\n\tposition: fixed;\n\ttop: 20px;\n\tright: 20px;\n\tpadding: 8px 15px 8px 15px;\n\tcolor: white;\n\tbackground-color: #455a64;\n\tborder-radius: 15px;\n\ttext-align: center;\n\tfont-size: 18px;\n\tbox-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.5);\n`;\n\nfunction FullScreenGallery(props) {\n\tconst { images, activeIndex, show, key } = useSelector((state) => state.imagePopupReducer);\n\tconst dispatch = useDispatch();\n\n\tconst [isLoaded, setLoaded] = useState(false);\n\tconst [queryString, setQueryString] = useState(\"\");\n\n\tconst history = useHistory();\n\n\tuseEffect(() => {\n\t\tif (show) {\n\t\t\treturn history.listen((location) => {\n\t\t\t\tdispatch(changeImagePopupSource({ show: false }));\n\t\t\t});\n\t\t}\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\n\t}, [show, history]);\n\n\tuseEffect(() => {\n\t\tif (!show) {\n\t\t\tsetLoaded(false);\n\t\t}\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\n\t}, [show]);\n\n\tconst hasImage = images && images.length > 0;\n\tconst hidden = !hasImage || !show;\n\n\tconst swipeConfig = {\n\t\tonSwipedLeft: () => {\n\t\t\tif (!images || images.length < 2 || !isLoaded) {\n\t\t\t\treturn;\n\t\t\t}\n\t\t\tsetLoaded(false);\n\t\t\tsetQueryString(\"\");\n\t\t\tdispatch(\n\t\t\t\tchangeImagePopupSource({\n\t\t\t\t\tactiveIndex: (activeIndex + 1) % images.length,\n\t\t\t\t})\n\t\t\t);\n\t\t},\n\t\tonSwipedRight: () => {\n\t\t\tif (!images || images.length < 2 || !isLoaded) {\n\t\t\t\treturn;\n\t\t\t}\n\t\t\tsetLoaded(false);\n\t\t\tsetQueryString(\"\");\n\t\t\tdispatch(\n\t\t\t\tchangeImagePopupSource({\n\t\t\t\t\tactiveIndex: (activeIndex + images.length - 1) % images.length,\n\t\t\t\t})\n\t\t\t);\n\t\t},\n\t\tonSwipedUp: () => {},\n\t\tonSwipedDown: () => {},\n\t\tpreventDefaultTouchmoveEvent: true,\n\t};\n\tconst imageCenterSwipeHandlers = useSwipeable(swipeConfig);\n\tconst buttonsSwipeHandlers = useSwipeable(swipeConfig);\n\n\tconst GalleryButton = (props) => {\n\t\tconst { className, clickToIndex } = props;\n\n\t\tconst [isHovered, setHovered] = useState(false);\n\n\t\treturn (\n\t\t\t<button\n\t\t\t\tclassName={`image-button ${className}`}\n\t\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\t\tonClick={(e) => {\n\t\t\t\t\te.preventDefault();\n\t\t\t\t\te.stopPropagation();\n\n\t\t\t\t\tsetHovered(false);\n\n\t\t\t\t\tif (!isLoaded) {\n\t\t\t\t\t\treturn;\n\t\t\t\t\t}\n\n\t\t\t\t\tsetLoaded(false);\n\t\t\t\t\tsetQueryString(\"\");\n\t\t\t\t\tdispatch(changeImagePopupSource({ activeIndex: clickToIndex }));\n\t\t\t\t}}\n\t\t\t\tstyle={{ ...(isHovered && isLoaded && { backgroundColor: \"rgba(3, 169, 244, 0.25)\" }) }}\n\t\t\t/>\n\t\t);\n\t};\n\n\treturn (\n\t\t<>\n\t\t\t{show && (\n\t\t\t\t<FullScreenGalleryWrapper\n\t\t\t\t\tshow={!hidden}\n\t\t\t\t\tloaded={isLoaded}\n\t\t\t\t\tonClick={() => dispatch(changeImagePopupSource({ show: false }))}\n\t\t\t\t\t{...imageCenterSwipeHandlers}\n\t\t\t\t>\n\t\t\t\t\t<picture>\n\t\t\t\t\t\t<source\n\t\t\t\t\t\t\ttype=\"image/webp\"\n\t\t\t\t\t\t\tsrcSet={\n\t\t\t\t\t\t\t\thasImage\n\t\t\t\t\t\t\t\t\t? encodeURIComponent(images[activeIndex].replace(/\\.(jpg|png|gif)/, \".webp\"))\n\t\t\t\t\t\t\t\t\t: null\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t/>\n\t\t\t\t\t\t<img\n\t\t\t\t\t\t\tkey={key}\n\t\t\t\t\t\t\tsrc={hasImage ? `${images[activeIndex]}${queryString}` : null}\n\t\t\t\t\t\t\talt={hasImage ? images[activeIndex].replace(/\\.(jpg|png|gif)/, \"\") : null}\n\t\t\t\t\t\t\tdraggable={false}\n\t\t\t\t\t\t\tonContextMenu={(e) => e.preventDefault()}\n\t\t\t\t\t\t\tonLoad={() => setLoaded(true)}\n\t\t\t\t\t\t\tonError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}\n\t\t\t\t\t\t/>\n\t\t\t\t\t</picture>\n\n\t\t\t\t\t{hasImage && images.length >= 2 && (\n\t\t\t\t\t\t<GalleryButtons loaded={isLoaded} {...buttonsSwipeHandlers}>\n\t\t\t\t\t\t\t<GalleryButton\n\t\t\t\t\t\t\t\tclassName=\"previous-image-button\"\n\t\t\t\t\t\t\t\tclickToIndex={(activeIndex + images.length - 1) % images.length}\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t<GalleryButton\n\t\t\t\t\t\t\t\tclassName=\"next-image-button\"\n\t\t\t\t\t\t\t\tclickToIndex={(activeIndex + 1) % images.length}\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t</GalleryButtons>\n\t\t\t\t\t)}\n\n\t\t\t\t\t{hasImage && images.length > 1 && (\n\t\t\t\t\t\t<NoSelect style={{ pointerEvents: \"none\" }}>\n\t\t\t\t\t\t\t<IndexAndCount>\n\t\t\t\t\t\t\t\t{activeIndex + 1}/{images.length}\n\t\t\t\t\t\t\t</IndexAndCount>\n\t\t\t\t\t\t</NoSelect>\n\t\t\t\t\t)}\n\n\t\t\t\t\t{!isLoaded && <LoadingIndicator />}\n\t\t\t\t</FullScreenGalleryWrapper>\n\t\t\t)}\n\t\t</>\n\t);\n}\n\nconst memoComponent = memo(FullScreenGallery, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as FullScreenGallery };\n";