export default "import { memo, useState } from \"react\";\nimport styled from \"styled-components\";\n\nconst ButtonWrapper = styled(({ hovered, capitalize, ...props }) => <div {...props} />)`\n\tbutton {\n\t\tdisplay: flex !important;\n\t\talign-items: center !important;\n\t\tjustify-content: center !important;\n\n\t\tpadding: 0.6em 1.25em !important;\n\t\tfont-size: 0.95em !important;\n\t\tfont-weight: bold !important;\n\t\t${({ capitalize }) => (capitalize ? \"text-transform: uppercase !important;\" : \"\")}\n\t\tcolor: white !important;\n\t\tcursor: pointer !important;\n\t\ttransition: 250ms ease-in-out !important;\n\t\tborder: none !important;\n\t\tborder-radius: 2em !important;\n\t\tbox-shadow: 0 0 1.5em ${({ hovered }) => (hovered ? \"#244ab6\" : \"#b63c18\")} !important;\n\t\tbackground-size: 300% 100% !important;\n\t\tbackground-image: linear-gradient(to right, #ff1744, #ff9100, #651fff, #00b0ff) !important;\n\t\t${({ hovered }) => (hovered ? \"background-position: 100% 0; !important\" : \"\")}\n\t}\n`;\n\nfunction GradientButton(props) {\n\tconst { children = \"\", capitalize, action, ...rest } = props;\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\treturn (\n\t\t<ButtonWrapper capitalize={capitalize} hovered={isHovered}>\n\t\t\t<button\n\t\t\t\t{...rest}\n\t\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\t\tonClick={() => {\n\t\t\t\t\tsetHovered(false);\n\t\t\t\t\tif (typeof action === \"function\") {\n\t\t\t\t\t\taction();\n\t\t\t\t\t}\n\t\t\t\t}}\n\t\t\t>\n\t\t\t\t{children}\n\t\t\t</button>\n\t\t</ButtonWrapper>\n\t);\n}\n\nconst memoComponent = memo(GradientButton, (prev, next) => prev.action === next.action);\nexport default memoComponent;\nexport { memoComponent as GradientButton };\n";