export default "import styled from \"styled-components\";\n\nconst MarkdownLink = styled(({ hovered, ...props }) => <span {...props} />)`\n\ta {\n\t\tcolor: ${({ hovered }) => (hovered ? \"#3f51b5\" : \"#0288d1\")} !important;\n\t\ttransition: 250ms;\n\t\tposition: relative;\n\t\tfont-weight: bold;\n\n\t\t&::after {\n\t\t\tcontent: \"\";\n\t\t\tposition: absolute;\n\t\t\tbottom: 0;\n\t\t\tleft: 0;\n\t\t\twidth: ${({ hovered }) => (hovered ? 100 : 0)}%;\n\t\t\theight: 1px;\n\t\t\tbackground: #3f51b5;\n\t\t\ttransition: 250ms;\n\t\t}\n\t}\n`;\n\nconst exports = { MarkdownLink };\nexport default exports;\nexport { MarkdownLink };\n";