export default "import { useState } from \"react\";\nimport { Link } from \"react-router-dom\";\nimport { HashLink } from \"react-router-hash-link\";\nimport { MarkdownLink } from \"../Styles\";\nimport startsWith from \"lodash/startsWith\";\n\nfunction CustomMarkdownLink(props) {\n\tconst { href, children } = props;\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\treturn (\n\t\t<MarkdownLink\n\t\t\thovered={isHovered}\n\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\tonClick={() => setHovered(false)}\n\t\t>\n\t\t\t{startsWith(href, \"#\") ? (\n\t\t\t\t<HashLink to={href} smooth replace className=\"allow-select\">\n\t\t\t\t\t{children}\n\t\t\t\t</HashLink>\n\t\t\t) : startsWith(href, \"http\") ? (\n\t\t\t\t<a href={href} target=\"_blank\" rel=\"external nofollow noopener noreferrer\" className=\"allow-select\">\n\t\t\t\t\t{children}\n\t\t\t\t</a>\n\t\t\t) : (\n\t\t\t\t<Link to={href} className=\"allow-select\">\n\t\t\t\t\t{children}\n\t\t\t\t</Link>\n\t\t\t)}\n\t\t</MarkdownLink>\n\t);\n}\n\nfunction CustomLinkRenderer(props) {\n\tconst { href, ...rest } = props;\n\n\treturn <CustomMarkdownLink href={decodeURIComponent(href)} {...rest} />;\n}\nexport default CustomLinkRenderer;\nexport { CustomLinkRenderer };\n";