export default "import { memo, useState } from \"react\";\nimport { Link } from \"react-router-dom\";\nimport { Helmet } from \"react-helmet\";\nimport {\n\tSegment,\n\tHeader,\n\tItem,\n\tCard,\n\tLabel,\n\tButton,\n\tIcon,\n\tImage,\n\tMessage,\n\tDivider,\n\tResponsive,\n} from \"semantic-ui-react\";\nimport styled from \"styled-components\";\nimport { Body, NoSelect } from \"../../../Styles\";\nimport { responsiveMaxWidth, responsiveMinWidth, blogPosts, blogPostTags } from \"../../../../config\";\nimport moment from \"moment\";\nimport get from \"lodash/get\";\nimport find from \"lodash/find\";\nimport union from \"lodash/union\";\nimport intersection from \"lodash/intersection\";\nimport without from \"lodash/without\";\nimport { LocalStorageService } from \"../../../../service\";\n\nconst CustomItemGroup = styled(Item.Group)`\n\ta {\n\t\ttransition: 250ms !important;\n\n\t\t&:hover {\n\t\t\tcolor: #e91e63 !important;\n\t\t}\n\t}\n\n\timg {\n\t\tmax-width: 80px !important;\n\t\tmax-height: 80px !important;\n\t\twidth: auto !important;\n\t\theight: auto !important;\n\t\tmargin: auto !important;\n\t}\n`;\n\nconst CustomCardGroup = styled(Item.Group)`\n\timg {\n\t\tmax-width: 40px !important;\n\t\tmax-height: 40px !important;\n\t\twidth: auto !important;\n\t\theight: auto !important;\n\t\tfloat: right !important;\n\t}\n`;\n\nconst HeaderLink = styled(({ hovered, ...props }) => <span {...props} />)`\n\ta {\n\t\ttransition: 250ms !important;\n\t\tcolor: ${({ hovered }) => (hovered ? \"#e91e63\" : \"black\")} !important;\n\t}\n`;\n\nfunction HeaderLinkWrapper(props) {\n\tconst { title, path } = props;\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\treturn (\n\t\t<HeaderLink hovered={isHovered}>\n\t\t\t<Link\n\t\t\t\tto={{ pathname: path, state: { fromAllBlogPosts: true } }}\n\t\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\t>\n\t\t\t\t{title}\n\t\t\t</Link>\n\t\t</HeaderLink>\n\t);\n}\n\nfunction Blog(props) {\n\tconst readBlogPostIdsFromLocalStorage = LocalStorageService.getReadBlogPostIds();\n\n\tconst allBlogPostTags = [\n\t\t...blogPostTags,\n\t\t...Array.from(new Set([...blogPosts.flatMap((e) => e.tags || [])]))\n\t\t\t.filter((e) => !find(blogPostTags, { name: e }))\n\t\t\t.map((e) => ({ name: e, icon: \"hashtag\", color: \"grey\" })),\n\t];\n\tconst [wantedTagNames, setWantedTagNames] = useState([]);\n\tconst [unwantedTagNames, setUnwantedTagNames] = useState([]);\n\n\tconst filteredBlogPosts = blogPosts.filter(\n\t\t({ tags = [] }) =>\n\t\t\t(wantedTagNames.length === 0 || intersection(tags, wantedTagNames).length > 0) &&\n\t\t\tintersection(tags, unwantedTagNames).length === 0\n\t);\n\n\tconst toBlogTags = (tags) => (\n\t\t<>\n\t\t\t{tags.map((tag, i) => (\n\t\t\t\t<Label\n\t\t\t\t\tkey={i}\n\t\t\t\t\tas=\"a\"\n\t\t\t\t\tcolor={get(find(allBlogPostTags, { name: tag }), \"color\", \"grey\")}\n\t\t\t\t\tonClick={() => setWantedTagNames(union(wantedTagNames, [tag]))}\n\t\t\t\t>\n\t\t\t\t\t<Icon name={get(find(allBlogPostTags, { name: tag }), \"icon\", \"hashtag\")} />\n\t\t\t\t\t{tag}\n\t\t\t\t</Label>\n\t\t\t))}\n\t\t</>\n\t);\n\n\treturn (\n\t\t<Body>\n\t\t\t<Helmet>\n\t\t\t\t<title>All Blog Posts | {process.env.REACT_APP_TITLE}</title>\n\t\t\t</Helmet>\n\n\t\t\t<Header size=\"huge\" style={{ cursor: \"default\" }}>\n\t\t\t\tAll Blog Posts\n\t\t\t</Header>\n\t\t\t<Divider section />\n\n\t\t\t<Segment vertical basic clearing>\n\t\t\t\t<NoSelect>\n\t\t\t\t\t<Label.Group style={{ textAlign: \"center\" }}>\n\t\t\t\t\t\t{allBlogPostTags.map((tag, i) => (\n\t\t\t\t\t\t\t<Label\n\t\t\t\t\t\t\t\tkey={i}\n\t\t\t\t\t\t\t\tas=\"a\"\n\t\t\t\t\t\t\t\tcolor={tag.color || \"grey\"}\n\t\t\t\t\t\t\t\tstyle={{\n\t\t\t\t\t\t\t\t\topacity:\n\t\t\t\t\t\t\t\t\t\twantedTagNames.includes(tag.name) || unwantedTagNames.includes(tag.name)\n\t\t\t\t\t\t\t\t\t\t\t? 1\n\t\t\t\t\t\t\t\t\t\t\t: 0.35,\n\t\t\t\t\t\t\t\t}}\n\t\t\t\t\t\t\t\tonClick={() => {\n\t\t\t\t\t\t\t\t\tif (wantedTagNames.includes(tag.name)) {\n\t\t\t\t\t\t\t\t\t\tsetWantedTagNames(without(wantedTagNames, tag.name));\n\t\t\t\t\t\t\t\t\t\tsetUnwantedTagNames(union(unwantedTagNames, [tag.name]));\n\t\t\t\t\t\t\t\t\t} else if (unwantedTagNames.includes(tag.name)) {\n\t\t\t\t\t\t\t\t\t\tsetUnwantedTagNames(without(unwantedTagNames, tag.name));\n\t\t\t\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\t\t\t\tsetWantedTagNames(union(wantedTagNames, [tag.name]));\n\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t}}\n\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t<Icon\n\t\t\t\t\t\t\t\t\tname={\n\t\t\t\t\t\t\t\t\t\t(wantedTagNames.includes(tag.name)\n\t\t\t\t\t\t\t\t\t\t\t? \"check\"\n\t\t\t\t\t\t\t\t\t\t\t: unwantedTagNames.includes(tag.name)\n\t\t\t\t\t\t\t\t\t\t\t? \"x\"\n\t\t\t\t\t\t\t\t\t\t\t: tag.icon) || \"hashtag\"\n\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t\t{tag.name}\n\t\t\t\t\t\t\t</Label>\n\t\t\t\t\t\t))}\n\t\t\t\t\t</Label.Group>\n\t\t\t\t</NoSelect>\n\t\t\t</Segment>\n\n\t\t\t{filteredBlogPosts && filteredBlogPosts.length > 0 ? (\n\t\t\t\t<Segment vertical>\n\t\t\t\t\t<Responsive as={CustomItemGroup} minWidth={responsiveMinWidth} divided relaxed>\n\t\t\t\t\t\t{filteredBlogPosts.map(({ postId, path, thumbnail, title, date, description, tags }, i) => (\n\t\t\t\t\t\t\t<Item key={i}>\n\t\t\t\t\t\t\t\t<Item.Image\n\t\t\t\t\t\t\t\t\tas={Link}\n\t\t\t\t\t\t\t\t\tto={{ pathname: path, state: { fromAllBlogPosts: true } }}\n\t\t\t\t\t\t\t\t\tsrc={thumbnail}\n\t\t\t\t\t\t\t\t\tsize=\"tiny\"\n\t\t\t\t\t\t\t\t\tstyle={{ margin: \"auto\" }}\n\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t\t<Item.Content>\n\t\t\t\t\t\t\t\t\t<Item.Header\n\t\t\t\t\t\t\t\t\t\tas={Link}\n\t\t\t\t\t\t\t\t\t\tto={{ pathname: path, state: { fromAllBlogPosts: true } }}\n\t\t\t\t\t\t\t\t\t\tclassName=\"allow-select\"\n\t\t\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t\t\t{title}\n\t\t\t\t\t\t\t\t\t\t{!readBlogPostIdsFromLocalStorage.includes(postId) && (\n\t\t\t\t\t\t\t\t\t\t\t<Label color=\"pink\" circular empty style={{ marginLeft: \"5px\" }} />\n\t\t\t\t\t\t\t\t\t\t)}\n\t\t\t\t\t\t\t\t\t</Item.Header>\n\t\t\t\t\t\t\t\t\t<Item.Meta style={{ cursor: \"default\" }}>\n\t\t\t\t\t\t\t\t\t\t<span>{moment(date).format(\"YYYY-MM-DD\")}</span>\n\t\t\t\t\t\t\t\t\t</Item.Meta>\n\t\t\t\t\t\t\t\t\t<Item.Description style={{ cursor: \"default\" }}>{description}</Item.Description>\n\t\t\t\t\t\t\t\t\t<Item.Extra>\n\t\t\t\t\t\t\t\t\t\t<Button\n\t\t\t\t\t\t\t\t\t\t\tas={Link}\n\t\t\t\t\t\t\t\t\t\t\tto={{ pathname: path, state: { fromAllBlogPosts: true } }}\n\t\t\t\t\t\t\t\t\t\t\tbasic\n\t\t\t\t\t\t\t\t\t\t\tcompact\n\t\t\t\t\t\t\t\t\t\t\tfloated=\"right\"\n\t\t\t\t\t\t\t\t\t\t\tcolor=\"pink\"\n\t\t\t\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t\t\t\tContinue reading\n\t\t\t\t\t\t\t\t\t\t\t<Icon name=\"right chevron\" />\n\t\t\t\t\t\t\t\t\t\t</Button>\n\t\t\t\t\t\t\t\t\t\t{tags && tags.length ? (\n\t\t\t\t\t\t\t\t\t\t\t<NoSelect>\n\t\t\t\t\t\t\t\t\t\t\t\t<Label.Group>{toBlogTags(tags)}</Label.Group>\n\t\t\t\t\t\t\t\t\t\t\t</NoSelect>\n\t\t\t\t\t\t\t\t\t\t) : null}\n\t\t\t\t\t\t\t\t\t</Item.Extra>\n\t\t\t\t\t\t\t\t</Item.Content>\n\t\t\t\t\t\t\t</Item>\n\t\t\t\t\t\t))}\n\t\t\t\t\t</Responsive>\n\n\t\t\t\t\t<Responsive\n\t\t\t\t\t\tas={CustomCardGroup}\n\t\t\t\t\t\tmaxWidth={responsiveMaxWidth}\n\t\t\t\t\t\tstyle={{ width: \"100%\", display: \"block\" }}\n\t\t\t\t\t>\n\t\t\t\t\t\t{filteredBlogPosts.map(({ postId, path, thumbnail, title, date, description, tags }, i) => (\n\t\t\t\t\t\t\t<Card\n\t\t\t\t\t\t\t\tkey={i}\n\t\t\t\t\t\t\t\tstyle={{ width: \"100%\", display: \"block\", background: \"rgba(255, 255, 255, 0.4)\" }}\n\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t<Card.Content>\n\t\t\t\t\t\t\t\t\t<Image\n\t\t\t\t\t\t\t\t\t\tas={Link}\n\t\t\t\t\t\t\t\t\t\tto={{ pathname: path, state: { fromAllBlogPosts: true } }}\n\t\t\t\t\t\t\t\t\t\tfloated=\"right\"\n\t\t\t\t\t\t\t\t\t\tsrc={thumbnail}\n\t\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t\t\t<Card.Header>\n\t\t\t\t\t\t\t\t\t\t<HeaderLinkWrapper title={title} path={path} />\n\t\t\t\t\t\t\t\t\t\t{!readBlogPostIdsFromLocalStorage.includes(postId) && (\n\t\t\t\t\t\t\t\t\t\t\t<Label color=\"pink\" circular empty style={{ marginLeft: \"5px\" }} />\n\t\t\t\t\t\t\t\t\t\t)}\n\t\t\t\t\t\t\t\t\t</Card.Header>\n\t\t\t\t\t\t\t\t\t<Card.Meta as={NoSelect}>{moment(date).format(\"YYYY-MM-DD\")}</Card.Meta>\n\t\t\t\t\t\t\t\t\t<Card.Description as={NoSelect}>{description}</Card.Description>\n\t\t\t\t\t\t\t\t</Card.Content>\n\t\t\t\t\t\t\t\t<Card.Content extra>\n\t\t\t\t\t\t\t\t\t<Button\n\t\t\t\t\t\t\t\t\t\tas={Link}\n\t\t\t\t\t\t\t\t\t\tto={{ pathname: path, state: { fromAllBlogPosts: true } }}\n\t\t\t\t\t\t\t\t\t\tbasic\n\t\t\t\t\t\t\t\t\t\tcompact\n\t\t\t\t\t\t\t\t\t\tfloated=\"right\"\n\t\t\t\t\t\t\t\t\t\tcolor=\"pink\"\n\t\t\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t\t\tRead\n\t\t\t\t\t\t\t\t\t\t<Icon name=\"right chevron\" />\n\t\t\t\t\t\t\t\t\t</Button>\n\t\t\t\t\t\t\t\t\t{tags && tags.length ? (\n\t\t\t\t\t\t\t\t\t\t<NoSelect>\n\t\t\t\t\t\t\t\t\t\t\t<Label.Group>{toBlogTags(tags)}</Label.Group>\n\t\t\t\t\t\t\t\t\t\t</NoSelect>\n\t\t\t\t\t\t\t\t\t) : null}\n\t\t\t\t\t\t\t\t</Card.Content>\n\t\t\t\t\t\t\t</Card>\n\t\t\t\t\t\t))}\n\t\t\t\t\t</Responsive>\n\t\t\t\t</Segment>\n\t\t\t) : (\n\t\t\t\t<NoSelect>\n\t\t\t\t\t<Message\n\t\t\t\t\t\tinfo\n\t\t\t\t\t\tfloating\n\t\t\t\t\t\tsize=\"huge\"\n\t\t\t\t\t\ticon=\"find\"\n\t\t\t\t\t\theader=\"No post found\"\n\t\t\t\t\t\tstyle={{ backgroundColor: \"transparent\", boxShadow: \"none\" }}\n\t\t\t\t\t/>\n\t\t\t\t</NoSelect>\n\t\t\t)}\n\t\t</Body>\n\t);\n}\n\nconst memoComponent = memo(Blog, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as Blog };\n";