export default "import { Fragment, memo, useState, useEffect } from \"react\";\nimport { Link, useHistory } from \"react-router-dom\";\nimport { Helmet } from \"react-helmet\";\nimport { Button, Grid, Header, Divider, Icon, Responsive } from \"semantic-ui-react\";\nimport styled from \"styled-components\";\nimport { MarkdownLink } from \"./Styles\";\nimport { PageNotFound } from \"./../PageNotFound\";\nimport { TocMarkdown } from \"./TocMarkdown\";\nimport { LocalStorageService } from \"../../../../service\";\nimport { MarkdownContent } from \"./MarkdownContent\";\nimport { ReadingProgressBar } from \"./ReadingProgressBar\";\nimport { Body } from \"../../../Styles\";\nimport { responsiveMaxWidth, responsiveMinWidth, blogPosts } from \"../../../../config\";\nimport first from \"lodash/first\";\nimport map from \"lodash/map\";\nimport filter from \"lodash/filter\";\nimport findIndex from \"lodash/findIndex\";\nimport find from \"lodash/find\";\nimport clamp from \"lodash/clamp\";\n\nconst HeaderWithAnchor = styled.div`\n\t.header-with-anchor:not(:hover) i::before {\n\t\tcontent: none !important;\n\t}\n\n\t.header-with-anchor i {\n\t\tcolor: #e91e63 !important;\n\t}\n\n\t.header-with-anchor-1 i {\n\t\tmargin-left: -45px !important;\n\t}\n\n\t.header-with-anchor-2 i {\n\t\tmargin-left: -40px !important;\n\t}\n\n\t.header-with-anchor-3 i {\n\t\tmargin-left: -32px !important;\n\t}\n\n\t.header-with-anchor-4 i {\n\t\tmargin-left: -28.5px !important;\n\t}\n\n\t.header-with-anchor-5 i {\n\t\tmargin-left: -28px !important;\n\t}\n\n\t.header-with-anchor-6 i {\n\t\tmargin-left: -22px !important;\n\t}\n`;\n\nconst FontSizeButtonGroup = styled(Button.Group)`\n\tposition: fixed;\n\tbottom: 20px;\n\tright: 100px;\n\tz-index: 1000;\n\tborder-radius: 4px;\n\tbox-shadow: 0 0 10px rgba(0, 0, 0, 0.5) !important;\n\n\tbutton {\n\t\tpadding: 0 !important;\n\t\twidth: 30px !important;\n\t\theight: 30px !important;\n\t}\n`;\n\nfunction MarkdownLinkWrapper(props) {\n\tconst { title, path } = props;\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\treturn (\n\t\t<MarkdownLink\n\t\t\thovered={isHovered}\n\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t>\n\t\t\t<Link className=\"allow-select\" to={path}>\n\t\t\t\t{title}\n\t\t\t</Link>\n\t\t</MarkdownLink>\n\t);\n}\n\nfunction BlogPost(props) {\n\tconst postId = props.match.params.postId;\n\n\tconst [fontSizeLevel, setFontSizeLevel] = useState(LocalStorageService.getBlogPostFontSizeLevel());\n\n\tconst currentPost = find(blogPosts, { postId });\n\n\tlet source = \"\";\n\ttry {\n\t\tsource = currentPost ? require(`!!raw-loader!../../../../${currentPost.src}`).default : \"\";\n\t} catch (e) {}\n\n\tconst history = useHistory();\n\n\tconst backToAllBlogPostsButton = (\n\t\t<div\n\t\t\tonClick={() => {\n\t\t\t\tif (window.history?.length >= 2 && history.location?.state?.fromAllBlogPosts) {\n\t\t\t\t\thistory.goBack();\n\t\t\t\t} else {\n\t\t\t\t\thistory.push(\"/blog\");\n\t\t\t\t}\n\t\t\t}}\n\t\t>\n\t\t\t<Responsive\n\t\t\t\tas={Button}\n\t\t\t\tminWidth={responsiveMinWidth}\n\t\t\t\tbasic\n\t\t\t\tcompact\n\t\t\t\tcolor=\"pink\"\n\t\t\t\tsize=\"medium\"\n\t\t\t\tcontent={\n\t\t\t\t\t<>\n\t\t\t\t\t\tBack to <b>All Blog Posts</b>\n\t\t\t\t\t</>\n\t\t\t\t}\n\t\t\t\ticon=\"arrow left\"\n\t\t\t\tlabelPosition=\"left\"\n\t\t\t/>\n\t\t\t<Responsive maxWidth={responsiveMaxWidth}>\n\t\t\t\t<Button basic compact color=\"pink\" size=\"medium\" icon=\"arrow left\" content=\"Back\" />\n\t\t\t</Responsive>\n\t\t</div>\n\t);\n\n\tconst newerPost = blogPosts[findIndex(blogPosts, (e) => e.postId === postId) - 1];\n\tconst newerButton = (\n\t\t<>\n\t\t\t<Responsive\n\t\t\t\tas={Button}\n\t\t\t\tminWidth={responsiveMinWidth}\n\t\t\t\tcompact\n\t\t\t\tcolor=\"violet\"\n\t\t\t\tsize=\"medium\"\n\t\t\t\tcontent={newerPost ? newerPost.title : \"Newer Post\"}\n\t\t\t\ticon=\"arrow left\"\n\t\t\t\tlabelPosition=\"left\"\n\t\t\t\tfloated=\"right\"\n\t\t\t\tstyle={{\n\t\t\t\t\topacity: newerPost === undefined ? 0.35 : 1,\n\t\t\t\t\tcursor: newerPost === undefined && \"not-allowed\",\n\t\t\t\t}}\n\t\t\t/>\n\t\t\t<Responsive maxWidth={responsiveMaxWidth}>\n\t\t\t\t<Button\n\t\t\t\t\tcompact\n\t\t\t\t\tcolor=\"violet\"\n\t\t\t\t\tsize=\"medium\"\n\t\t\t\t\ticon\n\t\t\t\t\tfloated=\"right\"\n\t\t\t\t\tstyle={{\n\t\t\t\t\t\topacity: newerPost === undefined ? 0.35 : 1,\n\t\t\t\t\t\tcursor: newerPost === undefined && \"not-allowed\",\n\t\t\t\t\t}}\n\t\t\t\t>\n\t\t\t\t\t<Icon name=\"arrow left\" />\n\t\t\t\t</Button>\n\t\t\t</Responsive>\n\t\t</>\n\t);\n\n\tconst olderPost = blogPosts[findIndex(blogPosts, (e) => e.postId === postId) + 1];\n\tconst olderButton = (\n\t\t<>\n\t\t\t<Responsive\n\t\t\t\tas={Button}\n\t\t\t\tminWidth={responsiveMinWidth}\n\t\t\t\tcompact\n\t\t\t\tcolor=\"purple\"\n\t\t\t\tsize=\"medium\"\n\t\t\t\tcontent={olderPost ? olderPost.title : \"Older Post\"}\n\t\t\t\ticon=\"arrow right\"\n\t\t\t\tlabelPosition=\"right\"\n\t\t\t\tfloated=\"right\"\n\t\t\t\tstyle={{\n\t\t\t\t\topacity: olderPost === undefined ? 0.35 : 1,\n\t\t\t\t\tcursor: olderPost === undefined && \"not-allowed\",\n\t\t\t\t}}\n\t\t\t/>\n\t\t\t<Responsive maxWidth={responsiveMaxWidth}>\n\t\t\t\t<Button\n\t\t\t\t\tcompact\n\t\t\t\t\tcolor=\"purple\"\n\t\t\t\t\tsize=\"medium\"\n\t\t\t\t\ticon\n\t\t\t\t\tfloated=\"right\"\n\t\t\t\t\tstyle={{\n\t\t\t\t\t\topacity: olderPost === undefined ? 0.35 : 1,\n\t\t\t\t\t\tcursor: olderPost === undefined && \"not-allowed\",\n\t\t\t\t\t}}\n\t\t\t\t>\n\t\t\t\t\t<Icon name=\"arrow right\" />\n\t\t\t\t</Button>\n\t\t\t</Responsive>\n\t\t</>\n\t);\n\n\tconst buttons = (\n\t\t<Grid columns={2}>\n\t\t\t<Grid.Column>{backToAllBlogPostsButton}</Grid.Column>\n\t\t\t<Grid.Column floated=\"right\">\n\t\t\t\t{olderPost && olderPost.path ? <Link to={olderPost.path}>{olderButton}</Link> : <>{olderButton}</>}\n\t\t\t\t{newerPost && newerPost.path ? <Link to={newerPost.path}>{newerButton}</Link> : <>{newerButton}</>}\n\t\t\t</Grid.Column>\n\t\t</Grid>\n\t);\n\n\tconst [readingProgress, setReadingProgress] = useState(0);\n\n\tuseEffect(() => {\n\t\tif (readingProgress >= 0.5) {\n\t\t\tLocalStorageService.addReadBlogPostId(postId);\n\t\t}\n\t}, [postId, readingProgress]);\n\n\tuseEffect(() => {\n\t\tconst scrollEventListener = (e) => {\n\t\t\tconst postContentElement = document.getElementById(\"post-content\");\n\t\t\tconst readProgress = clamp(\n\t\t\t\tpostContentElement &&\n\t\t\t\t\tdocument.scrollingElement &&\n\t\t\t\t\tdocument.documentElement &&\n\t\t\t\t\t(document.scrollingElement.scrollTop -\n\t\t\t\t\t\tpostContentElement.offsetTop +\n\t\t\t\t\t\tdocument.scrollingElement.offsetHeight / 2) /\n\t\t\t\t\t\t(postContentElement.scrollHeight - document.scrollingElement.offsetHeight / 2),\n\t\t\t\t0,\n\t\t\t\t1\n\t\t\t);\n\n\t\t\tsetReadingProgress(readProgress);\n\t\t};\n\n\t\tdocument.addEventListener(\"scroll\", scrollEventListener);\n\n\t\treturn () => {\n\t\t\tdocument.removeEventListener(\"scroll\", scrollEventListener);\n\t\t};\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\n\t}, []);\n\n\treturn (\n\t\t<>\n\t\t\t<ReadingProgressBar progress={readingProgress} />\n\n\t\t\t{currentPost && (\n\t\t\t\t<FontSizeButtonGroup floated=\"right\">\n\t\t\t\t\t<Button\n\t\t\t\t\t\tcolor=\"black\"\n\t\t\t\t\t\tdisabled={fontSizeLevel === 0}\n\t\t\t\t\t\tonClick={() => {\n\t\t\t\t\t\t\tif (fontSizeLevel > 0) {\n\t\t\t\t\t\t\t\tsetFontSizeLevel((prevLevel) => {\n\t\t\t\t\t\t\t\t\tconst newLevel = prevLevel - 1;\n\t\t\t\t\t\t\t\t\tLocalStorageService.setBlogPostFontSizeLevel(newLevel);\n\t\t\t\t\t\t\t\t\treturn newLevel;\n\t\t\t\t\t\t\t\t});\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t}}\n\t\t\t\t\t\tstyle={{ width: \"50px\", fontSize: \"12px\" }}\n\t\t\t\t\t>\n\t\t\t\t\t\tA\n\t\t\t\t\t</Button>\n\t\t\t\t\t<Button\n\t\t\t\t\t\tcolor=\"black\"\n\t\t\t\t\t\tdisabled={fontSizeLevel === 3}\n\t\t\t\t\t\tonClick={() => {\n\t\t\t\t\t\t\tif (fontSizeLevel < 3) {\n\t\t\t\t\t\t\t\tsetFontSizeLevel((prevLevel) => {\n\t\t\t\t\t\t\t\t\tconst newLevel = prevLevel + 1;\n\t\t\t\t\t\t\t\t\tLocalStorageService.setBlogPostFontSizeLevel(newLevel);\n\t\t\t\t\t\t\t\t\treturn newLevel;\n\t\t\t\t\t\t\t\t});\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t}}\n\t\t\t\t\t\tstyle={{ width: \"50px\", fontSize: \"18px\" }}\n\t\t\t\t\t>\n\t\t\t\t\t\tA\n\t\t\t\t\t</Button>\n\t\t\t\t</FontSizeButtonGroup>\n\t\t\t)}\n\n\t\t\t<Body>\n\t\t\t\t<Helmet>\n\t\t\t\t\t<title>\n\t\t\t\t\t\t{currentPost ? currentPost.title : \"404 Not Found\"} | Blog | {process.env.REACT_APP_TITLE}\n\t\t\t\t\t</title>\n\t\t\t\t</Helmet>\n\n\t\t\t\t{currentPost ? (\n\t\t\t\t\t<HeaderWithAnchor>\n\t\t\t\t\t\t{buttons}\n\t\t\t\t\t\t<Divider section />\n\n\t\t\t\t\t\t<Header as=\"h1\" style={{ cursor: \"default\" }}>\n\t\t\t\t\t\t\t{currentPost.title}\n\t\t\t\t\t\t</Header>\n\n\t\t\t\t\t\t{currentPost.previousPostId && (\n\t\t\t\t\t\t\t<Header as=\"h4\" style={{ textAlign: \"center\", lineHeight: \"1.8em\" }}>\n\t\t\t\t\t\t\t\t<div style={{ cursor: \"default\" }}>\n\t\t\t\t\t\t\t\t\t<b>Continued from previous post:</b>\n\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t<MarkdownLinkWrapper\n\t\t\t\t\t\t\t\t\ttitle={first(\n\t\t\t\t\t\t\t\t\t\tmap(filter(blogPosts, { postId: currentPost.previousPostId }), \"title\")\n\t\t\t\t\t\t\t\t\t)}\n\t\t\t\t\t\t\t\t\tpath={first(map(filter(blogPosts, { postId: currentPost.previousPostId }), \"path\"))}\n\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t</Header>\n\t\t\t\t\t\t)}\n\n\t\t\t\t\t\t<div\n\t\t\t\t\t\t\tstyle={{ display: \"flex\", justifyContent: \"center\", alignItems: \"center\", margin: \"3em 0\" }}\n\t\t\t\t\t\t>\n\t\t\t\t\t\t\t<img\n\t\t\t\t\t\t\t\tsrc={currentPost.thumbnail}\n\t\t\t\t\t\t\t\talt={currentPost.thumbnail.replace(/\\.(jpg|png|gif)/, \"\")}\n\t\t\t\t\t\t\t\tdraggable={false}\n\t\t\t\t\t\t\t\tonContextMenu={(e) => e.preventDefault()}\n\t\t\t\t\t\t\t\tstyle={{\n\t\t\t\t\t\t\t\t\twidth: \"50%\",\n\t\t\t\t\t\t\t\t\tmaxHeight: \"400px\",\n\t\t\t\t\t\t\t\t\tobjectFit: \"contain\",\n\t\t\t\t\t\t\t\t}}\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t\t<TocMarkdown markdown={source} />\n\n\t\t\t\t\t\t<MarkdownContent markdown={source} fontSizeLevel={fontSizeLevel} />\n\n\t\t\t\t\t\t{filter(blogPosts, { previousPostId: currentPost.postId }).length > 0 && (\n\t\t\t\t\t\t\t<Header as=\"h4\" style={{ textAlign: \"center\", lineHeight: \"1.8em\", paddingTop: \"2em\" }}>\n\t\t\t\t\t\t\t\t<div style={{ cursor: \"default\" }}>\n\t\t\t\t\t\t\t\t\t<b>\n\t\t\t\t\t\t\t\t\t\tNext Post\n\t\t\t\t\t\t\t\t\t\t{filter(blogPosts, { previousPostId: currentPost.postId }).length > 1\n\t\t\t\t\t\t\t\t\t\t\t? \"s\"\n\t\t\t\t\t\t\t\t\t\t\t: \"\"}\n\t\t\t\t\t\t\t\t\t\t:\n\t\t\t\t\t\t\t\t\t</b>\n\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t{filter(blogPosts, { previousPostId: currentPost.postId }).map((post, i, arr) => (\n\t\t\t\t\t\t\t\t\t<Fragment key={i}>\n\t\t\t\t\t\t\t\t\t\t<MarkdownLinkWrapper title={post.title} path={post.path} />\n\t\t\t\t\t\t\t\t\t\t{i < arr.length - 1 && <br />}\n\t\t\t\t\t\t\t\t\t</Fragment>\n\t\t\t\t\t\t\t\t))}\n\t\t\t\t\t\t\t</Header>\n\t\t\t\t\t\t)}\n\n\t\t\t\t\t\t{source && (\n\t\t\t\t\t\t\t<>\n\t\t\t\t\t\t\t\t<Divider section />\n\n\t\t\t\t\t\t\t\t{buttons}\n\t\t\t\t\t\t\t</>\n\t\t\t\t\t\t)}\n\t\t\t\t\t</HeaderWithAnchor>\n\t\t\t\t) : (\n\t\t\t\t\t<PageNotFound />\n\t\t\t\t)}\n\t\t\t</Body>\n\t\t</>\n\t);\n}\n\nconst memoComponent = memo(BlogPost, (prev, next) => prev.match.params.postId === next.match.params.postId);\nexport default memoComponent;\nexport { memoComponent as BlogPost };\n";