export default "import { memo, useState } from \"react\";\nimport styled from \"styled-components\";\nimport { NoSelect } from \"../../../Styles\";\n\nconst HightlightedTextStyle = styled(NoSelect)`\n\tdisplay: inline-block;\n\tcolor: ${({ hovered }) => (hovered ? \"purple\" : \"gold\")};\n\tbackground: ${({ hovered }) => (hovered ? \"gold\" : \"purple\")};\n\t${({ hovered }) => (hovered ? \"cursor: pointer;\" : \"\")}\n\tfont-size: 1.25em;\n\tfont-weight: bold;\n\tpadding: 2px 8px 2px 8px;\n\tmargin: 0 3px 0 3px;\n\tborder-radius: 5px;\n\ttransition: 250ms;\n\t-webkit-box-reflect: below 0em -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgba(255, 255, 255, 0.1)));\n`;\n\nconst openLink = (url) => window.open(url, \"_blank\", \"noopener\");\n\nfunction HightlightedText(props) {\n\tconst { url } = props;\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\treturn (\n\t\t<HightlightedTextStyle\n\t\t\t{...props}\n\t\t\thovered={isHovered}\n\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\tonClick={() => {\n\t\t\t\tsetHovered(false);\n\t\t\t\topenLink(url);\n\t\t\t}}\n\t\t/>\n\t);\n}\n\nconst memoComponent = memo(HightlightedText, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as HightlightedText };\n";