export default "import { memo, useState } from \"react\";\nimport styled from \"styled-components\";\nimport { NoSelect } from \"../Styles\";\nimport { LocalStorageService } from \"../../service\";\n\nconst CookiesConsentWrapper = styled(({ consent, ...props }) => <div {...props} />)`\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-items: center;\n\n\tposition: fixed;\n\tbottom: ${({ consent }) => (consent === 2 ? 0 : \"-100%\")};\n\twidth: 100%;\n\tpadding: 0.75em 1em;\n\tfont-size: 16px;\n\tcolor: #c1d5e0;\n\tbackground-color: rgba(38, 50, 56, 0.95);\n\tbox-shadow: 0 0 10px rgba(0, 0, 0, 0.5);\n\tz-index: 2000;\n\tcursor: default;\n\n\tanimation: ${({ consent }) => (consent === 2 ? \"close\" : \"expand\")} 1.5s forwards;\n\t${({ consent }) => (consent === 2 ? \"\" : \"animation-delay: 1s;\")}\n\n\t@keyframes expand {\n\t\t100% {\n\t\t\tbottom: 0;\n\t\t}\n\t}\n\n\t@keyframes close {\n\t\t100% {\n\t\t\tbottom: -100%;\n\t\t}\n\t}\n`;\n\nconst CookiesConsentButton = styled.div`\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-items: center;\n\n\tpadding: 10px 15px;\n\tmargin-left: 1em;\n\tz-index: 2001;\n\tborder-radius: 5px;\n\tcursor: pointer;\n\tfont-size: 15px;\n\tfont-weight: bold;\n\tcolor: #f57f17;\n\tbackground-color: #fdd835;\n\twhite-space: nowrap;\n\ttransition: 250ms !important;\n\n\t&:hover {\n\t\tfilter: brightness(0.85);\n\t}\n`;\n\nfunction CookiesConsent(props) {\n\t// 1 means already accepted before\n\t// 2 means just accepted\n\t// else means not accepted yet\n\tconst [consent, setConsent] = useState(LocalStorageService.getCookiesConsent());\n\n\treturn (\n\t\t<>\n\t\t\t{consent !== 1 && (\n\t\t\t\t<NoSelect>\n\t\t\t\t\t<CookiesConsentWrapper consent={consent}>\n\t\t\t\t\t\tThis website uses cookies, local storage and/or similar technologies to store user settings so\n\t\t\t\t\t\tas to improve user experience.\n\t\t\t\t\t\t<CookiesConsentButton\n\t\t\t\t\t\t\tonClick={() => {\n\t\t\t\t\t\t\t\tsetConsent(2);\n\t\t\t\t\t\t\t\tLocalStorageService.setCookiesConsent(true);\n\t\t\t\t\t\t\t}}\n\t\t\t\t\t\t>\n\t\t\t\t\t\t\tAccept\n\t\t\t\t\t\t</CookiesConsentButton>\n\t\t\t\t\t</CookiesConsentWrapper>\n\t\t\t\t</NoSelect>\n\t\t\t)}\n\t\t</>\n\t);\n}\n\nconst memoComponent = memo(CookiesConsent, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as CookiesConsent };\n";