export default "import { CHANGE_IMAGE_POPUP_SOURCE } from \"../action/actionTypes\";\n\nconst imagePopupReducer = (\n\tstate = {\n\t\timages: [],\n\t\tactiveIndex: null,\n\t\tshow: false,\n\t\tkey: null,\n\t},\n\t{ type, data }\n) => {\n\tswitch (type) {\n\t\tcase CHANGE_IMAGE_POPUP_SOURCE:\n\t\t\tconst hasImages = data.images && data.images.length > 0;\n\t\t\treturn {\n\t\t\t\t...state,\n\t\t\t\t...data,\n\t\t\t\tshow: (data.show === undefined && (data.activeIndex >= 0 || hasImages)) || data.show,\n\t\t\t\tkey:\n\t\t\t\t\thasImages || (typeof data.show === \"boolean\" && state.show !== data.show)\n\t\t\t\t\t\t? Math.random()\n\t\t\t\t\t\t: state.key,\n\t\t\t};\n\t\tdefault:\n\t\t\treturn state;\n\t}\n};\n\nexport default imagePopupReducer;\nexport { imagePopupReducer };\n";