export default "import { memo, useState } from \"react\";\nimport { Link } from \"react-router-dom\";\nimport { Grid, Label, Segment, Header, Icon, Responsive } from \"semantic-ui-react\";\nimport styled from \"styled-components\";\nimport { Gallery } from \"../reusable/Gallery\";\nimport { GradientButton } from \"../reusable/GradientButton\";\nimport { NoSelect, GradientText } from \"../Styles\";\n// import { ImageSlideshow } from \"../reusable/slideshow/ImageSlideshow\";\n// import { MobileImageSlideshow } from \"../reusable/slideshow/MobileImageSlideshow\";\nimport moment from \"moment\";\nimport { responsiveMaxWidth, responsiveMinWidth } from \"../../config\";\nimport isEmpty from \"lodash/isEmpty\";\n\nconst HeaderLinkWrapper = styled(({ hovered, ...props }) => <span {...props} />)`\n\ta {\n\t\ttransition: 250ms !important;\n\t\tcolor: ${({ hovered }) => (hovered ? \"#e91e63\" : \"black\")} !important;\n\t}\n`;\n\nfunction Project(props) {\n\tconst { detail, inverted } = props;\n\tconst { path, projectName, projectNature, year, images, descriptionOverview, description } = detail;\n\n\tconst leftToRight = inverted !== true;\n\tconst colors = [\"#b71c1c\", \"#0d47a1\", \"#1b5e20\", \"#e65100\", \"#3e2723\"];\n\tconst yearColor = Number.isInteger(year) ? colors[(moment().year() % year) % colors.length] : \"#212121\";\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\tconst constructImage = () => (\n\t\t<Grid.Column width={8} textAlign=\"center\" verticalAlign={isEmpty(images) ? \"middle\" : null}>\n\t\t\t{(images || []).length > 0 ? (\n\t\t\t\t<div style={{ top: \"20px\", position: \"sticky\" }}>\n\t\t\t\t\t<Segment\n\t\t\t\t\t\traised\n\t\t\t\t\t\tstyle={{\n\t\t\t\t\t\t\tbackgroundColor: \"transparent\",\n\t\t\t\t\t\t\tborderRadius: \"1.5em\",\n\t\t\t\t\t\t\tpadding: 0,\n\t\t\t\t\t\t}}\n\t\t\t\t\t>\n\t\t\t\t\t\t<Gallery images={images} width=\"100%\" height=\"400px\" />\n\t\t\t\t\t</Segment>\n\t\t\t\t</div>\n\t\t\t) : (\n\t\t\t\t<Responsive as={NoSelect} minWidth={responsiveMinWidth}>\n\t\t\t\t\t<Label size=\"large\">\n\t\t\t\t\t\t<Icon name=\"dont\" />\n\t\t\t\t\t\tNo screenshot available\n\t\t\t\t\t</Label>\n\t\t\t\t</Responsive>\n\t\t\t)}\n\t\t</Grid.Column>\n\t);\n\n\tconst constructDescription = () => (\n\t\t<Grid.Column width={8}>\n\t\t\t<div style={{ top: \"20px\", position: \"sticky\" }}>\n\t\t\t\t<NoSelect>\n\t\t\t\t\t<Responsive\n\t\t\t\t\t\tas={Label}\n\t\t\t\t\t\tminWidth={responsiveMinWidth}\n\t\t\t\t\t\ttag\n\t\t\t\t\t\tsize=\"large\"\n\t\t\t\t\t\tstyle={{\n\t\t\t\t\t\t\tcolor: \"white\",\n\t\t\t\t\t\t\tbackgroundColor: yearColor,\n\t\t\t\t\t\t\tfloat: !leftToRight && \"right\",\n\t\t\t\t\t\t\ttransform: !leftToRight && \"scaleX(-1)\",\n\t\t\t\t\t\t}}\n\t\t\t\t\t>\n\t\t\t\t\t\t<div style={{ transform: !leftToRight && \"scaleX(-1)\" }}>\n\t\t\t\t\t\t\t{Number.isInteger(year) ? \"Year \" + year : year || \"Others\"}\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</Responsive>\n\t\t\t\t\t<Responsive\n\t\t\t\t\t\tas={Label}\n\t\t\t\t\t\tmaxWidth={responsiveMaxWidth}\n\t\t\t\t\t\ttag\n\t\t\t\t\t\tsize=\"large\"\n\t\t\t\t\t\tstyle={{\n\t\t\t\t\t\t\tcolor: \"white\",\n\t\t\t\t\t\t\tbackgroundColor: yearColor,\n\t\t\t\t\t\t}}\n\t\t\t\t\t>\n\t\t\t\t\t\t{Number.isInteger(year) ? \"Year \" + year : year || \"Others\"}\n\t\t\t\t\t</Responsive>\n\t\t\t\t</NoSelect>\n\t\t\t\t<Header as=\"h2\" style={{ cursor: \"default\" }}>\n\t\t\t\t\t<HeaderLinkWrapper hovered={isHovered}>\n\t\t\t\t\t\t<Link\n\t\t\t\t\t\t\tclassName=\"allow-select\"\n\t\t\t\t\t\t\tto={{ pathname: path, state: { fromAllProjects: true } }}\n\t\t\t\t\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\t\t\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\t\t\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\t\t\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\t\t\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\t\t\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\t\t\t\t>\n\t\t\t\t\t\t\t{projectName || \"\"}\n\t\t\t\t\t\t</Link>\n\t\t\t\t\t</HeaderLinkWrapper>\n\t\t\t\t\t<Header.Subheader>{projectNature || \"\"}</Header.Subheader>\n\t\t\t\t</Header>\n\t\t\t\t<GradientText>{description || descriptionOverview || \"\"}</GradientText>\n\n\t\t\t\t<div style={{ display: \"flex\", justifyContent: \"flex-end\", alignItems: \"center\", height: \"3em\" }}>\n\t\t\t\t\t<Link to={{ pathname: path, state: { fromAllProjects: true } }}>\n\t\t\t\t\t\t<Responsive as={GradientButton} minWidth={responsiveMinWidth} capitalize>\n\t\t\t\t\t\t\tLearn More\n\t\t\t\t\t\t\t<Icon name=\"chevron right\" size=\"large\" style={{ margin: \"0 -0.25em 0 0.25em\" }} />\n\t\t\t\t\t\t</Responsive>\n\t\t\t\t\t\t<Responsive as={GradientButton} maxWidth={responsiveMaxWidth} capitalize>\n\t\t\t\t\t\t\tDetails\n\t\t\t\t\t\t\t<Icon name=\"chevron right\" size=\"large\" style={{ margin: \"0 -0.25em 0 0.25em\" }} />\n\t\t\t\t\t\t</Responsive>\n\t\t\t\t\t</Link>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t</Grid.Column>\n\t);\n\n\treturn (\n\t\t<Grid stackable reversed={leftToRight && \"mobile\"} relaxed>\n\t\t\t{leftToRight ? (\n\t\t\t\t<>\n\t\t\t\t\t{constructImage()}\n\t\t\t\t\t{constructDescription()}\n\t\t\t\t</>\n\t\t\t) : (\n\t\t\t\t<>\n\t\t\t\t\t{constructDescription()}\n\t\t\t\t\t{constructImage()}\n\t\t\t\t</>\n\t\t\t)}\n\t\t</Grid>\n\t);\n}\n\nconst memoComponent = memo(Project, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as Project };\n";