export default "import styled from \"styled-components\";\nimport { Button } from \"semantic-ui-react\";\n\nconst ScrollToTopButton = styled(({ navMenuOpen, ...props }) => <Button {...props} />)`\n\tbottom: 75px;\n\tright: 20px;\n\tposition: fixed;\n\tz-index: 1001;\n\ttransition: all 250ms, right 0ms !important;\n\tbox-shadow: 0 0 10px rgba(0, 0, 0, 0.5) !important;\n\n\t@media (hover: hover) {\n\t\tright: ${({ navMenuOpen }) => (navMenuOpen ? \"32px\" : \"20px\")};\n\t}\n`;\n\nconst NavMenuButtonShadow = styled(({ navMenuOpen, ...props }) => <div {...props} />)`\n\twidth: 40px;\n\theight: 40px;\n\tbottom: 20px;\n\tright: 20px;\n\tposition: fixed;\n\tmargin-right: 4.5px;\n\tz-index: 1001;\n\tborder-radius: 10px;\n\ttransition: all 250ms, right 0ms !important;\n\tbackground: linear-gradient(-45deg, #ffa63d, #ff3d77, #338aff, #3cf0c5);\n\tbackground-size: 600%;\n\tanimation: animated-button 16s linear infinite !important;\n\tfilter: blur(15px);\n\tpointer-events: none;\n\n\t@media (hover: hover) {\n\t\tright: ${({ navMenuOpen }) => (navMenuOpen ? \"32px\" : \"20px\")};\n\t}\n`;\n\nconst ButtonsBackground = styled(({ show, navMenuOpen, ...props }) => <div {...props} />)`\n\tposition: fixed;\n\tdisplay: block;\n\tbottom: 0;\n\tright: 0;\n\twidth: 200px;\n\theight: 275px;\n\tz-index: 1000;\n\tpointer-events: none;\n\ttransition: 0ms !important;\n\tbackground: linear-gradient(\n\t\tto bottom right,\n\t\ttransparent,\n\t\ttransparent,\n\t\ttransparent,\n\t\trgba(0, 0, 0, 0.25),\n\t\trgba(0, 0, 0, 0.7)\n\t);\n\t${({ show }) => (show ? \"\" : \"opacity: 0;\")}\n\n\t@media (hover: hover) {\n\t\tright: ${({ navMenuOpen }) => (navMenuOpen ? \"0px\" : \"-12px\")};\n\t}\n`;\n\nconst exports = { ScrollToTopButton, NavMenuButtonShadow, ButtonsBackground };\nexport default exports;\nexport { ScrollToTopButton, NavMenuButtonShadow, ButtonsBackground };\n";