export default "import { memo, useState } from \"react\";\nimport { Header } from \"semantic-ui-react\";\nimport { HashLink } from \"react-router-hash-link\";\nimport styled from \"styled-components\";\nimport toc from \"markdown-toc-unlazy\";\n\nconst CustomHeader = styled(({ hovered, ...props }) => <div {...props} />)`\n\ta {\n\t\tdisplay: block !important;\n\t\tfont-size: 1.25em !important;\n\t\tcolor: ${({ hovered }) => (hovered ? \"#e91e63\" : \"#455a64\")} !important;\n\t\tbackground: ${({ hovered }) =>\n\t\t\thovered\n\t\t\t\t? \"linear-gradient(to right, rgba(244, 143, 177, 0.25), rgba(176, 190, 197, 0.25), rgba(176, 190, 197, 0.25))\"\n\t\t\t\t: \"rgba(176, 190, 197, 0.25)\"} !important;\n\t\ttransition: 250ms;\n\t}\n`;\n\nfunction HeaderWrapper(props) {\n\tconst [isHovered, setHovered] = useState(false);\n\n\treturn (\n\t\t<CustomHeader\n\t\t\thovered={isHovered}\n\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\tonClick={() => setHovered(false)}\n\t\t>\n\t\t\t<Header {...props} />\n\t\t</CustomHeader>\n\t);\n}\n\nfunction TocMarkdown(props) {\n\tconst { markdown } = props;\n\n\tconst tableOfContents = markdown ? toc(markdown).json : null;\n\tconst minLevel = markdown ? Math.min(...tableOfContents.map((toc) => toc.lvl)) : null;\n\n\treturn (\n\t\t<>\n\t\t\t{markdown && (\n\t\t\t\t<div style={{ paddingBottom: \"40px\" }}>\n\t\t\t\t\t{tableOfContents.map((toc, i) => (\n\t\t\t\t\t\t<HeaderWrapper\n\t\t\t\t\t\t\tkey={i}\n\t\t\t\t\t\t\tas={HashLink}\n\t\t\t\t\t\t\tto={\"#\" + toc.slug}\n\t\t\t\t\t\t\tsmooth\n\t\t\t\t\t\t\treplace\n\t\t\t\t\t\t\tblock\n\t\t\t\t\t\t\tattached={i === 0 ? \"top\" : i === tableOfContents.length - 1 ? \"bottom\" : true}\n\t\t\t\t\t\t\tstyle={{ paddingLeft: (toc.lvl + 1 - minLevel) * 20 - 5 + \"px\" }}\n\t\t\t\t\t\t>\n\t\t\t\t\t\t\t{toc.content.replace(/`/g, \"\")}\n\t\t\t\t\t\t</HeaderWrapper>\n\t\t\t\t\t))}\n\t\t\t\t</div>\n\t\t\t)}\n\t\t</>\n\t);\n}\n\nconst memoComponent = memo(TocMarkdown, (prev, next) => prev.markdown === next.markdown);\nexport default memoComponent;\nexport { memoComponent as TocMarkdown };\n";