export default "const getCookiesConsent = () => Number(localStorage.getItem(\"cookiesConsent\")) || \"0\";\n\nconst setCookiesConsent = (accepted) => localStorage.setItem(\"cookiesConsent\", accepted ? \"1\" : \"0\");\n\nconst getReadBlogPostIds = () => (localStorage.getItem(\"readBlogPostIds\") || \"\").split(\",\").filter((e) => e) || [];\n\nconst addReadBlogPostId = (postId) => {\n\tconst readBlogPostIds = getReadBlogPostIds();\n\tlocalStorage.setItem(\"readBlogPostIds\", Array.from(new Set([...readBlogPostIds, postId])).join(\",\"));\n};\n\nconst getBlogPostFontSizeLevel = () => Number(localStorage.getItem(\"fontSizeLevel\")) || 1;\n\nconst setBlogPostFontSizeLevel = (fontSizeLevel) => localStorage.setItem(\"fontSizeLevel\", fontSizeLevel);\n\nconst LocalStorageService = {\n\tgetCookiesConsent,\n\tsetCookiesConsent,\n\tgetReadBlogPostIds,\n\taddReadBlogPostId,\n\tgetBlogPostFontSizeLevel,\n\tsetBlogPostFontSizeLevel,\n};\n\nexport default LocalStorageService;\nexport {\n\tgetCookiesConsent,\n\tsetCookiesConsent,\n\tgetReadBlogPostIds,\n\taddReadBlogPostId,\n\tgetBlogPostFontSizeLevel,\n\tsetBlogPostFontSizeLevel,\n};\n";