export default "import { useState, useEffect } from \"react\";\nimport { HashRouter } from \"react-router-dom\";\nimport { HashLink } from \"react-router-hash-link\";\nimport { useSelector, useDispatch } from \"react-redux\";\nimport { Eportfolio } from \"./components/Eportfolio\";\nimport Particles from \"react-particles-js\";\nimport { FullScreenMenu } from \"./components/global/FullScreenMenu\";\nimport { defaultConfig, snowConfig } from \"./config/particles\";\nimport { ScrollToTopButton, NavMenuButtonShadow, ButtonsBackground } from \"./components/global/button/AppButtons\";\nimport { NavMenuButton } from \"./components/global/button/NavMenuButton\";\nimport { CookiesConsent } from \"./components/global/CookiesConsent\";\nimport { ScrollUtils } from \"./utils\";\nimport { changeImagePopupSource } from \"./redux/action\";\n\nconst particlesConfig = new Date().getMonth() === 11 ? snowConfig : defaultConfig;\n\nfunction App(props) {\n\tconst { show: isImagePopupVisible } = useSelector((state) => state.imagePopupReducer);\n\n\tconst [isScrollToTopButtonVisible, setScrollToTopButtonVisible] = useState(false);\n\tconst [isNavMenuOpen, setNavMenuOpen] = useState(false);\n\n\tconst dispatch = useDispatch();\n\n\tuseEffect(() => {\n\t\tsetScrollToTopButtonVisible(ScrollUtils.isScrollToTopButtonVisible());\n\n\t\tconst escapeKeyListener = (e) => {\n\t\t\tif (e.keyCode === 27) {\n\t\t\t\te.preventDefault();\n\t\t\t\te.stopPropagation();\n\n\t\t\t\tsetNavMenuOpen(false);\n\n\t\t\t\tif (isImagePopupVisible) {\n\t\t\t\t\tdispatch(changeImagePopupSource({ show: false }));\n\t\t\t\t}\n\t\t\t}\n\t\t};\n\n\t\tconst scrollEventListener = (e) => setScrollToTopButtonVisible(ScrollUtils.isScrollToTopButtonVisible());\n\n\t\tdocument.addEventListener(\"keydown\", escapeKeyListener);\n\t\tdocument.addEventListener(\"scroll\", scrollEventListener);\n\n\t\treturn () => {\n\t\t\tdocument.removeEventListener(\"keydown\", escapeKeyListener);\n\t\t\tdocument.removeEventListener(\"scroll\", scrollEventListener);\n\t\t};\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\n\t}, [isImagePopupVisible]);\n\n\treturn (\n\t\t<>\n\t\t\t<HashRouter>\n\t\t\t\t<HashLink to=\"#top\" smooth replace>\n\t\t\t\t\t<ScrollToTopButton\n\t\t\t\t\t\ticon=\"chevron up\"\n\t\t\t\t\t\tcompact\n\t\t\t\t\t\tcircular\n\t\t\t\t\t\tsize=\"big\"\n\t\t\t\t\t\tcolor=\"yellow\"\n\t\t\t\t\t\tstyle={{\n\t\t\t\t\t\t\topacity: !isScrollToTopButtonVisible || isNavMenuOpen || isImagePopupVisible ? 0 : 1,\n\t\t\t\t\t\t\tpointerEvents:\n\t\t\t\t\t\t\t\t!isScrollToTopButtonVisible || isNavMenuOpen || isImagePopupVisible ? \"none\" : false,\n\t\t\t\t\t\t}}\n\t\t\t\t\t\tnavMenuOpen={isNavMenuOpen}\n\t\t\t\t\t/>\n\t\t\t\t</HashLink>\n\n\t\t\t\t<CookiesConsent />\n\n\t\t\t\t<NavMenuButton\n\t\t\t\t\tnavMenuOpen={isNavMenuOpen}\n\t\t\t\t\tshow={!isImagePopupVisible}\n\t\t\t\t\taction={() => setNavMenuOpen(!isNavMenuOpen)}\n\t\t\t\t/>\n\t\t\t\t<NavMenuButtonShadow style={{ opacity: isImagePopupVisible ? 0 : 1 }} navMenuOpen={isNavMenuOpen} />\n\n\t\t\t\t<ButtonsBackground\n\t\t\t\t\tid=\"nav-menu-buttons-background\"\n\t\t\t\t\tshow={!isImagePopupVisible}\n\t\t\t\t\tnavMenuOpen={isNavMenuOpen}\n\t\t\t\t/>\n\n\t\t\t\t<FullScreenMenu navMenuOpen={isNavMenuOpen} menuItemCallback={() => setNavMenuOpen(false)} />\n\n\t\t\t\t<div\n\t\t\t\t\tstyle={{\n\t\t\t\t\t\twidth: \"100%\",\n\t\t\t\t\t\topacity: isNavMenuOpen ? 0 : 1,\n\t\t\t\t\t\ttransition: \"250ms\",\n\t\t\t\t\t}}\n\t\t\t\t>\n\t\t\t\t\t<Eportfolio historyChangeCallback={() => setScrollToTopButtonVisible(false)} />\n\t\t\t\t</div>\n\t\t\t</HashRouter>\n\n\t\t\t<Particles className=\"particles-wrapper\" params={particlesConfig} />\n\t\t</>\n\t);\n}\n\nexport default App;\nexport { App };\n";