export default "import { memo, useState, useEffect } from \"react\";\nimport styled from \"styled-components\";\n\nconst NavMenuButtonWrapper = styled(({ hovered, navMenuOpen, ...props }) => <div {...props} />)`\n\twidth: 40px;\n\theight: 40px;\n\tbottom: 20px;\n\tright: 20px;\n\tposition: fixed;\n\tmargin-right: 4.5px;\n\tz-index: 1002;\n\tborder-radius: 10px;\n\tcursor: pointer;\n\ttransition: all 250ms, right 0ms !important;\n\tbackground: linear-gradient(-45deg, #ffa63d, #ff3d77, #338aff, #3cf0c5);\n\tbackground-size: 600%;\n\tanimation: animated-button 16s linear infinite !important;\n\tbox-shadow: 0 0 10px rgba(0, 0, 0, 0.5) !important;\n\n\t${({ hovered }) =>\n\t\thovered\n\t\t\t? `\n\tfilter: brightness(0.8);\n\n\tspan.middle {\n\t\topacity: ${({ navMenuOpen }) => (navMenuOpen ? 0 : 1)};\n\t}\n`\n\t\t\t: \"\"}\n\n\t@keyframes animated-button {\n\t\t0% {\n\t\t\tbackground-position: 0% 50%;\n\t\t}\n\t\t50% {\n\t\t\tbackground-position: 100% 50%;\n\t\t}\n\t\t100% {\n\t\t\tbackground-position: 0% 50%;\n\t\t}\n\t}\n\n\t@media (hover: hover) {\n\t\tright: ${({ navMenuOpen }) => (navMenuOpen ? \"32px\" : \"20px\")};\n\t}\n`;\n\nconst NavMenuButtonLine = styled(({ navMenuOpen, ...props }) => <span {...props} />)`\n\tbackground: white;\n\twidth: 24px;\n\theight: 4px;\n\tposition: absolute;\n\ttop: 9px;\n\tleft: 8px;\n\ttransition: 0.35s ease;\n\n\t&:nth-of-type(1) {\n\t\ttransform: ${({ navMenuOpen }) => (navMenuOpen ? \"translateY(9px) translateX(0) rotate(45deg)\" : \"\")};\n\t}\n\n\t&:nth-of-type(2) {\n\t\ttop: 18px;\n\t\t${({ navMenuOpen }) => (navMenuOpen ? \"opacity: 0;\" : \"\")}\n\t}\n\n\t&:nth-of-type(3) {\n\t\ttop: 27px;\n\t\ttransform: ${({ navMenuOpen }) => (navMenuOpen ? \"translateY(-9px) translateX(0) rotate(-45deg)\" : \"\")};\n\t}\n`;\n\nfunction NavMenuButton(props) {\n\tconst { navMenuOpen, show, action } = props;\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\tuseEffect(() => {\n\t\tdocument.body.style.overflow = navMenuOpen ? \"hidden\" : null;\n\t}, [navMenuOpen]);\n\n\treturn (\n\t\t<NavMenuButtonWrapper\n\t\t\thovered={isHovered}\n\t\t\tnavMenuOpen={navMenuOpen}\n\t\t\tstyle={{\n\t\t\t\topacity: show ? 1 : 0,\n\t\t\t\t...(!show && { pointerEvents: \"none\" }),\n\t\t\t}}\n\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\tonClick={() => {\n\t\t\t\tsetHovered(false);\n\t\t\t\tif (typeof action === \"function\") {\n\t\t\t\t\taction();\n\t\t\t\t}\n\t\t\t}}\n\t\t>\n\t\t\t<NavMenuButtonLine className=\"top\" navMenuOpen={navMenuOpen} />\n\t\t\t<NavMenuButtonLine className=\"middle\" navMenuOpen={navMenuOpen} />\n\t\t\t<NavMenuButtonLine className=\"bottom\" navMenuOpen={navMenuOpen} />\n\t\t</NavMenuButtonWrapper>\n\t);\n}\n\nconst memoComponent = memo(\n\tNavMenuButton,\n\t(prev, next) => prev.navMenuOpen === next.navMenuOpen && prev.show === next.show && prev.action === next.action\n);\nexport default memoComponent;\nexport { memoComponent as NavMenuButton };\n";