export default "import { useCallback } from \"react\";\nimport { useLocation } from \"react-router-dom\";\nimport { HashLink } from \"react-router-hash-link\";\nimport { Header, Icon, Responsive } from \"semantic-ui-react\";\nimport isEmpty from \"lodash/isEmpty\";\nimport filter from \"lodash/filter\";\nimport { ScrollUtils } from \"../../../../../utils\";\nimport { responsiveMinWidth } from \"../../../../../config\";\n\nfunction Heading(props) {\n\tconst { anchorRef, level, children } = props;\n\n\tconst location = useLocation();\n\n\tconst ref = useCallback(\n\t\t(node) => {\n\t\t\tif (node && location.hash && anchorRef === decodeURIComponent(location.hash.replace(/#/, \"\"))) {\n\t\t\t\tScrollUtils.scrollToElement(node, 500);\n\t\t\t}\n\t\t},\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\n\t\t[]\n\t);\n\n\treturn (\n\t\t<Header as={\"h\" + level} className={`header-with-anchor header-with-anchor-${level}`}>\n\t\t\t<HashLink ref={ref} id={anchorRef} to={\"#\" + anchorRef} smooth replace>\n\t\t\t\t<Responsive as={Icon} minWidth={responsiveMinWidth} name=\"linkify\" />\n\t\t\t</HashLink>\n\t\t\t{children}\n\t\t</Header>\n\t);\n}\n\nfunction CustomHeadingRenderer(props) {\n\tconst { tocArray, level, children = [] } = props;\n\n\tconst headingText = children.map((e) => (typeof e === \"string\" ? e : e.props.children.join(\"\"))).join(\"\");\n\tconst anchorRef = isEmpty(tocArray) ? null : (filter(tocArray, { content: headingText })[0] || {}).slug;\n\n\treturn (\n\t\t<Heading anchorRef={anchorRef} level={level}>\n\t\t\t{children}\n\t\t</Heading>\n\t);\n}\nexport default CustomHeadingRenderer;\nexport { CustomHeadingRenderer };\n";