export default "import { memo } from \"react\";\nimport { Message } from \"semantic-ui-react\";\nimport { NoSelect } from \"../../Styles\";\n\nfunction PageNotFound(props) {\n\treturn (\n\t\t<NoSelect>\n\t\t\t<Message\n\t\t\t\terror\n\t\t\t\tfloating\n\t\t\t\tsize=\"huge\"\n\t\t\t\ticon=\"find\"\n\t\t\t\theader=\"404 Not found\"\n\t\t\t\tcontent=\"Sorry, we are not able to locate the webpage you are looking for.\"\n\t\t\t\tstyle={{ backgroundColor: \"transparent\", boxShadow: \"none\" }}\n\t\t\t/>\n\t\t</NoSelect>\n\t);\n}\n\nconst memoComponent = memo(PageNotFound, (prev, next) => prev.match.params.postId === next.match.params.postId);\nexport default memoComponent;\nexport { memoComponent as PageNotFound };\n";