export default "import { memo, useState } from \"react\";\nimport { Helmet } from \"react-helmet\";\nimport { Header, Button, Form, Message, Icon, Modal, Transition } from \"semantic-ui-react\";\nimport styled from \"styled-components\";\nimport { Body, NoSelect } from \"../../Styles\";\nimport { GradientButton } from \"../../reusable/GradientButton\";\nimport { CommentService, NetworkService } from \"../../../service\";\nimport get from \"lodash/get\";\nimport mapValues from \"lodash/mapValues\";\n\nconst errorMessageMap = mapValues(\n\t{\n\t\t1: {\n\t\t\theader: \"Missing email/phone\",\n\t\t\tcontent: \"You must enter either your email or phone number.\",\n\t\t},\n\t\t2: {\n\t\t\theader: \"Missing message\",\n\t\t\tcontent: \"You must enter a message.\",\n\t\t},\n\t\t3: {\n\t\t\theader: \"Invalid email address\",\n\t\t\tcontent: \"The format of the email address you enter is invalid.\",\n\t\t},\n\t\t4: {\n\t\t\theader: \"Invalid phone number\",\n\t\t\tcontent: \"The format of the phone number you enter is invalid.\",\n\t\t},\n\t},\n\t(v) => ({\n\t\t...v,\n\t\tcontent: v.content + \" Please double-check your input.\",\n\t\theaderIcon: \"ban\",\n\t\tbuttonIcon: \"write\",\n\t\tbutton: \"Try again\",\n\t})\n);\n\nconst ContactFormWrapper = styled.div`\n\tinput,\n\ttextarea {\n\t\tbackground-color: rgba(255, 255, 255, 0.35) !important;\n\t\tcolor: #8e24aa !important;\n\t}\n\n\tform::before {\n\t\tbackground-color: transparent !important;\n\t}\n`;\n\nfunction Contact(props) {\n\tconst [firstName, setFirstName] = useState(\"\");\n\tconst [lastName, setLastName] = useState(\"\");\n\tconst [phone, setPhone] = useState(\"\");\n\tconst [email, setEmail] = useState(\"\");\n\tconst [message, setMessage] = useState(\"\");\n\tconst [isSendButtonClicked, setSendButtonClicked] = useState(false);\n\tconst [isSent, setSent] = useState(0); // 0 = not sent, 1 = sending, 2 = sent\n\tconst [isErrorPopupOpen, setErrorPopupOpen] = useState(false);\n\n\tconst errorMissingEmailOrPhone =\n\t\t!!email || !!phone\n\t\t\t? false\n\t\t\t: isSendButtonClicked\n\t\t\t? {\n\t\t\t\t\tcontent: \"You must enter your email and/or phone no.\",\n\t\t\t\t\tpointing: \"above\",\n\t\t\t  }\n\t\t\t: true;\n\tconst errorMissingMessage = !!message\n\t\t? false\n\t\t: isSendButtonClicked\n\t\t? {\n\t\t\t\tcontent: \"You must leave a message.\",\n\t\t\t\tpointing: \"above\",\n\t\t  }\n\t\t: true;\n\tconst errorInvalidEmail =\n\t\t!!email && !new RegExp(\"^\\\\w+@\\\\w+\\\\.\\\\w+$\").test(email)\n\t\t\t? {\n\t\t\t\t\tcontent: \"You must enter a valid email.\",\n\t\t\t\t\tpointing: \"above\",\n\t\t\t  }\n\t\t\t: false;\n\tconst errorInvalidPhone =\n\t\t!!phone && !new RegExp(\"^(\\\\+?852)?\\\\d{8}$\").test(phone.replace(/\\s/g, \"\"))\n\t\t\t? {\n\t\t\t\t\tcontent: \"You must enter a valid HK phone no.\",\n\t\t\t\t\tpointing: \"above\",\n\t\t\t  }\n\t\t\t: false;\n\tconst errorMessageType = errorMissingEmailOrPhone\n\t\t? 1\n\t\t: errorMissingMessage\n\t\t? 2\n\t\t: errorInvalidEmail\n\t\t? 3\n\t\t: errorInvalidPhone\n\t\t? 4\n\t\t: 0;\n\tconst isValidInput = errorMessageType === 0;\n\n\tconst sendToFirebase = () => {\n\t\tNetworkService.getClientIp()\n\t\t\t.then((response) => {\n\t\t\t\tif (!response || !response.data) {\n\t\t\t\t\treturn;\n\t\t\t\t}\n\n\t\t\t\tconst clientIp = (response.data.split(\"\\n\").filter((e) => e.startsWith(\"ip=\"))[0] || \"\").replace(\n\t\t\t\t\t/^ip=/,\n\t\t\t\t\t\"\"\n\t\t\t\t);\n\n\t\t\t\tCommentService.postComment({\n\t\t\t\t\tfirstName,\n\t\t\t\t\tlastName,\n\t\t\t\t\tphone,\n\t\t\t\t\temail,\n\t\t\t\t\tmessage,\n\t\t\t\t\tclientIp,\n\t\t\t\t})\n\t\t\t\t\t.then((response) => {\n\t\t\t\t\t\tsetSent(2);\n\t\t\t\t\t\tconsole.log(\"Successfully sent.\");\n\t\t\t\t\t})\n\t\t\t\t\t.catch((error) => {\n\t\t\t\t\t\tsetSent(0);\n\t\t\t\t\t\talert(\"Failed to send (2).\");\n\t\t\t\t\t\tconsole.log(error);\n\t\t\t\t\t});\n\t\t\t})\n\t\t\t.catch((error) => {\n\t\t\t\tsetSent(0);\n\t\t\t\talert(\"Failed to send (1).\");\n\t\t\t\tconsole.log(error);\n\t\t\t});\n\t};\n\n\treturn (\n\t\t<Body>\n\t\t\t<Helmet>\n\t\t\t\t<title>Contact | {process.env.REACT_APP_TITLE}</title>\n\t\t\t</Helmet>\n\n\t\t\t<Header size=\"huge\" style={{ cursor: \"default\" }}>\n\t\t\t\tContact\n\t\t\t</Header>\n\n\t\t\t<Transition visible={isErrorPopupOpen} animation=\"scale\" duration={400}>\n\t\t\t\t<Modal open={isErrorPopupOpen}>\n\t\t\t\t\t<Header\n\t\t\t\t\t\ticon={get(errorMessageMap[errorMessageType], \"headerIcon\", \"\")}\n\t\t\t\t\t\tcontent={<NoSelect>{get(errorMessageMap[errorMessageType], \"header\", \"\")}</NoSelect>}\n\t\t\t\t\t/>\n\t\t\t\t\t<Modal.Content>\n\t\t\t\t\t\t<NoSelect>{get(errorMessageMap[errorMessageType], \"content\", \"\")}</NoSelect>\n\t\t\t\t\t</Modal.Content>\n\t\t\t\t\t<Modal.Actions>\n\t\t\t\t\t\t<Button color=\"red\" onClick={() => setErrorPopupOpen(false)}>\n\t\t\t\t\t\t\t<Icon name={get(errorMessageMap[errorMessageType], \"buttonIcon\", \"\")} />{\" \"}\n\t\t\t\t\t\t\t{get(errorMessageMap[errorMessageType], \"button\", \"\")}\n\t\t\t\t\t\t</Button>\n\t\t\t\t\t</Modal.Actions>\n\t\t\t\t</Modal>\n\t\t\t</Transition>\n\n\t\t\t{isSent === 2 ? (\n\t\t\t\t<NoSelect>\n\t\t\t\t\t<Message\n\t\t\t\t\t\tinfo\n\t\t\t\t\t\tfloating\n\t\t\t\t\t\tsize=\"huge\"\n\t\t\t\t\t\ticon\n\t\t\t\t\t\tstyle={{ backgroundColor: \"transparent\", boxShadow: \"none\" }}\n\t\t\t\t\t>\n\t\t\t\t\t\t<span style={{ fontSize: \"3em\" }}>👌</span>\n\t\t\t\t\t\t<Message.Content>\n\t\t\t\t\t\t\t<Message.Header>Successfully Sent</Message.Header>\n\t\t\t\t\t\t\tAppreciate your time, I'll get back to you soon, cheers!\n\t\t\t\t\t\t</Message.Content>\n\t\t\t\t\t</Message>\n\t\t\t\t</NoSelect>\n\t\t\t) : (\n\t\t\t\t<>\n\t\t\t\t\t<ContactFormWrapper>\n\t\t\t\t\t\t<Form size=\"big\" loading={isSent > 0}>\n\t\t\t\t\t\t\t<Form.Group widths=\"equal\">\n\t\t\t\t\t\t\t\t<Form.Input\n\t\t\t\t\t\t\t\t\tfluid\n\t\t\t\t\t\t\t\t\tlabel=\"First name\"\n\t\t\t\t\t\t\t\t\tplaceholder=\"First name (optional)\"\n\t\t\t\t\t\t\t\t\tvalue={firstName}\n\t\t\t\t\t\t\t\t\tdisabled={isSent > 0}\n\t\t\t\t\t\t\t\t\tonChange={(e) => setFirstName(e.target.value)}\n\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t\t<Form.Input\n\t\t\t\t\t\t\t\t\tfluid\n\t\t\t\t\t\t\t\t\tlabel=\"Last name\"\n\t\t\t\t\t\t\t\t\tplaceholder=\"Last name (optional)\"\n\t\t\t\t\t\t\t\t\tvalue={lastName}\n\t\t\t\t\t\t\t\t\tdisabled={isSent > 0}\n\t\t\t\t\t\t\t\t\tonChange={(e) => setLastName(e.target.value)}\n\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t</Form.Group>\n\t\t\t\t\t\t\t<Form.Group widths=\"equal\">\n\t\t\t\t\t\t\t\t<Form.Input\n\t\t\t\t\t\t\t\t\tfluid\n\t\t\t\t\t\t\t\t\tlabel=\"Email\"\n\t\t\t\t\t\t\t\t\tplaceholder=\"example@gmail.com\"\n\t\t\t\t\t\t\t\t\trequired\n\t\t\t\t\t\t\t\t\terror={errorMissingEmailOrPhone || errorInvalidEmail}\n\t\t\t\t\t\t\t\t\tvalue={email}\n\t\t\t\t\t\t\t\t\tdisabled={isSent > 0}\n\t\t\t\t\t\t\t\t\tonChange={(e) => setEmail(e.target.value)}\n\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t\t<Form.Input\n\t\t\t\t\t\t\t\t\tfluid\n\t\t\t\t\t\t\t\t\tlabel=\"Phone Number\"\n\t\t\t\t\t\t\t\t\tplaceholder=\"Mine: 6330 1333\"\n\t\t\t\t\t\t\t\t\trequired\n\t\t\t\t\t\t\t\t\terror={errorMissingEmailOrPhone || errorInvalidPhone}\n\t\t\t\t\t\t\t\t\tvalue={phone}\n\t\t\t\t\t\t\t\t\tdisabled={isSent > 0}\n\t\t\t\t\t\t\t\t\tonChange={(e) => setPhone(e.target.value)}\n\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t</Form.Group>\n\t\t\t\t\t\t\t<Form.TextArea\n\t\t\t\t\t\t\t\trows={8}\n\t\t\t\t\t\t\t\tlabel=\"Message\"\n\t\t\t\t\t\t\t\tplaceholder=\"Leave me a message :)\"\n\t\t\t\t\t\t\t\trequired\n\t\t\t\t\t\t\t\terror={errorMissingMessage}\n\t\t\t\t\t\t\t\tvalue={message}\n\t\t\t\t\t\t\t\tdisabled={isSent > 0}\n\t\t\t\t\t\t\t\tonChange={(e) => setMessage(e.target.value)}\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t</Form>\n\t\t\t\t\t</ContactFormWrapper>\n\t\t\t\t\t<div style={{ display: \"flex\", justifyContent: \"center\", alignItems: \"center\", marginTop: \"2em\" }}>\n\t\t\t\t\t\t<GradientButton\n\t\t\t\t\t\t\tcapitalize\n\t\t\t\t\t\t\tstyle={{ ...(isSent > 0 && { opacity: 0.5, pointerEvents: \"none\" }) }}\n\t\t\t\t\t\t\taction={\n\t\t\t\t\t\t\t\tisSent > 0\n\t\t\t\t\t\t\t\t\t? null\n\t\t\t\t\t\t\t\t\t: () => {\n\t\t\t\t\t\t\t\t\t\t\tsetSendButtonClicked(true);\n\t\t\t\t\t\t\t\t\t\t\tif (!isValidInput) {\n\t\t\t\t\t\t\t\t\t\t\t\tsetErrorPopupOpen(true);\n\t\t\t\t\t\t\t\t\t\t\t\treturn;\n\t\t\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\t\t\tsetSent(1);\n\t\t\t\t\t\t\t\t\t\t\tsendToFirebase();\n\t\t\t\t\t\t\t\t\t  }\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t>\n\t\t\t\t\t\t\tSend\n\t\t\t\t\t\t</GradientButton>\n\t\t\t\t\t</div>\n\t\t\t\t</>\n\t\t\t)}\n\t\t</Body>\n\t);\n}\n\nconst memoComponent = memo(Contact, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as Contact };\n";