export default "const isScrollToTopButtonVisible = () =>\n\tdocument.scrollingElement && document.scrollingElement.scrollTop >= document.scrollingElement.offsetHeight * 0.75;\n\nconst isScrolledToBottom = () =>\n\tMath.abs(\n\t\tdocument.scrollingElement.scrollTop +\n\t\t\tdocument.scrollingElement.offsetHeight -\n\t\t\tdocument.scrollingElement.scrollHeight\n\t) < 20;\n\nconst scrollToElement = (element, delay) =>\n\tsetTimeout(() => {\n\t\tif (\n\t\t\t// element.getBoundingClientRect().top means how much window's top is above the element's top\n\t\t\t(isScrolledToBottom() && element.getBoundingClientRect().top < 0) ||\n\t\t\t(!isScrolledToBottom() && Math.abs(element.getBoundingClientRect().top) > 20)\n\t\t) {\n\t\t\telement.scrollIntoView(true);\n\t\t\tscrollToElement(element, 250);\n\t\t}\n\t}, delay);\n\nconst ScrollUtils = {\n\tisScrollToTopButtonVisible,\n\tisScrolledToBottom,\n\tscrollToElement,\n};\n\nexport default ScrollUtils;\nexport { isScrollToTopButtonVisible, isScrolledToBottom, scrollToElement };\n";