export default "import { memo, useEffect } from \"react\";\nimport { Route, Redirect, Switch, useHistory } from \"react-router-dom\";\n// import { useTransition animated } from \"react-spring\";\nimport { SiteHeader } from \"./header/SiteHeader\";\nimport { Home, WorkExp, HobbyProject, ProjectDetail, Personality, Blog, BlogPost, Contact } from \"./center/pages\";\nimport { SiteFooter } from \"./footer/SiteFooter\";\nimport { FullScreenGallery } from \"./reusable/FullScreenGallery\";\n\nfunction Eportfolio(props) {\n\tconst { historyChangeCallback } = props;\n\n\t// const location = useLocation();\n\t// const transitions = useTransition(location, (location) => location.pathname, {\n\t// \tfrom: { opacity: 0, position: \"absolute\" },\n\t// \tenter: { opacity: 1 },\n\t// \tleave: { opacity: 0 },\n\t// });\n\n\tconst history = useHistory();\n\n\tuseEffect(() => {\n\t\treturn history.listen((location) => {\n\t\t\tif (!location.hash && history.action === \"PUSH\") {\n\t\t\t\twindow.scrollTo({ top: 0 });\n\t\t\t}\n\n\t\t\tif (historyChangeCallback) {\n\t\t\t\thistoryChangeCallback();\n\t\t\t}\n\t\t});\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\n\t}, [history]);\n\n\treturn (\n\t\t<>\n\t\t\t<SiteHeader />\n\n\t\t\t{/* {transitions.map(({ item, props, key }) => (\n\t\t\t\t<animated.div key={key} style={{ ...props, width: \"100%\" }}> */}\n\t\t\t<Switch /*location={item}*/>\n\t\t\t\t<Route exact sensitive path=\"/\" component={Home} />\n\t\t\t\t<Route\n\t\t\t\t\texact\n\t\t\t\t\tsensitive\n\t\t\t\t\tpath=\"/workExp/:projectId\"\n\t\t\t\t\trender={(routeProps) => (\n\t\t\t\t\t\t<ProjectDetail {...routeProps} projectType=\"My Work Experiences\" backPath=\"/workExp\" />\n\t\t\t\t\t)}\n\t\t\t\t/>\n\t\t\t\t<Route exact sensitive path=\"/workExp\" component={WorkExp} />\n\t\t\t\t<Route\n\t\t\t\t\texact\n\t\t\t\t\tsensitive\n\t\t\t\t\tpath=\"/hobbyProject/:projectId\"\n\t\t\t\t\trender={(routeProps) => (\n\t\t\t\t\t\t<ProjectDetail {...routeProps} projectType=\"My Hobby Projects\" backPath=\"/hobbyProject\" />\n\t\t\t\t\t)}\n\t\t\t\t/>\n\t\t\t\t<Route exact sensitive path=\"/hobbyProject\" component={HobbyProject} />\n\t\t\t\t<Route exact sensitive path=\"/personality\" component={Personality} />\n\t\t\t\t<Route exact sensitive path=\"/blog/:postId\" component={BlogPost} />\n\t\t\t\t<Route exact sensitive path=\"/blog\" component={Blog} />\n\t\t\t\t<Route exact sensitive path=\"/contact\" component={Contact} />\n\t\t\t\t<Redirect to=\"/\" />\n\t\t\t</Switch>\n\t\t\t<SiteFooter />\n\t\t\t<FullScreenGallery />\n\t\t\t{/* </animated.div>\n\t\t\t))} */}\n\t\t</>\n\t);\n}\n\nconst memoComponent = memo(Eportfolio, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as Eportfolio };\n";