export default "import { memo } from \"react\";\nimport { Grid, Icon, Header, Divider, Responsive } from \"semantic-ui-react\";\nimport { ContactItem } from \"./ContactItem\";\nimport { Body, NoSelect } from \"./../Styles\";\nimport { responsiveMaxWidth, responsiveMinWidth } from \"../../config\";\nimport preval from \"preval.macro\";\nimport GitInfo from \"react-git-info/macro\";\nimport moment from \"moment\";\n\nfunction SiteFooter(props) {\n\tconst currentYear = new Date().getFullYear();\n\tconst lastUpdatedTime = preval`module.exports = new Date().getTime();`;\n\n\tconst gitInfo = GitInfo();\n\n\treturn (\n\t\t<Body>\n\t\t\t<Divider horizontal section>\n\t\t\t\t<Header as=\"h5\" style={{ opacity: 0.5 }}>\n\t\t\t\t\t<Icon name=\"react\" />\n\t\t\t\t\tReach Me Now!\n\t\t\t\t</Header>\n\t\t\t</Divider>\n\t\t\t<Grid stackable padded columns={3}>\n\t\t\t\t<Grid.Column>\n\t\t\t\t\t<ContactItem\n\t\t\t\t\t\thref=\"tel:+85263301333\"\n\t\t\t\t\t\ticon=\"phone\"\n\t\t\t\t\t\ticonRotation=\"clockwise\"\n\t\t\t\t\t\tcolor=\"teal\"\n\t\t\t\t\t\theader=\"Mobile\"\n\t\t\t\t\t\tsubHeader=\"6330 1333\"\n\t\t\t\t\t/>\n\t\t\t\t</Grid.Column>\n\t\t\t\t<Grid.Column>\n\t\t\t\t\t<ContactItem\n\t\t\t\t\t\thref=\"mailto:michaelboyboy@gmail.com\"\n\t\t\t\t\t\ticon=\"mail\"\n\t\t\t\t\t\tcolor=\"red\"\n\t\t\t\t\t\theader=\"Email\"\n\t\t\t\t\t\tsubHeader=\"michaelboyboy@gmail.com\"\n\t\t\t\t\t/>\n\t\t\t\t</Grid.Column>\n\t\t\t\t<Grid.Column>\n\t\t\t\t\t<ContactItem\n\t\t\t\t\t\thref=\"https://hk.linkedin.com/in/mickchung\"\n\t\t\t\t\t\ticon=\"linkedin\"\n\t\t\t\t\t\tcolor=\"blue\"\n\t\t\t\t\t\theader=\"LinkedIn\"\n\t\t\t\t\t\tsubHeader=\"hk.linkedin.com/in/mickchung\"\n\t\t\t\t\t/>\n\t\t\t\t</Grid.Column>\n\t\t\t</Grid>\n\n\t\t\t<Divider />\n\n\t\t\t{[\n\t\t\t\t{\n\t\t\t\t\tprops: {\n\t\t\t\t\t\tmaxWidth: responsiveMaxWidth,\n\t\t\t\t\t},\n\t\t\t\t\tstyle: {\n\t\t\t\t\t\tdiv: {\n\t\t\t\t\t\t\tfontSize: \"2.75vw\",\n\t\t\t\t\t\t},\n\t\t\t\t\t},\n\t\t\t\t},\n\t\t\t\t{\n\t\t\t\t\tprops: {\n\t\t\t\t\t\tminWidth: responsiveMinWidth,\n\t\t\t\t\t},\n\t\t\t\t\tstyle: {\n\t\t\t\t\t\tlastUpdatedOn: {\n\t\t\t\t\t\t\tfloat: \"right\",\n\t\t\t\t\t\t},\n\t\t\t\t\t},\n\t\t\t\t},\n\t\t\t].map(({ props = {}, style = {} }, i) => (\n\t\t\t\t<Responsive key={i} {...props}>\n\t\t\t\t\t<div style={{ color: \"#4ba3c7\", ...(style.div || {}) }}>\n\t\t\t\t\t\t<NoSelect style={{ display: \"inline-block\" }}>\n\t\t\t\t\t\t\tCopyright © {currentYear} Chung Cheuk Hang Michael. All rights reserved.\n\t\t\t\t\t\t</NoSelect>\n\t\t\t\t\t\t<NoSelect style={{ display: \"inline-block\", ...(style.lastUpdatedOn || {}) }}>\n\t\t\t\t\t\t\tLast Updated On:{\" \"}\n\t\t\t\t\t\t\t<span\n\t\t\t\t\t\t\t\ttitle={moment(lastUpdatedTime).format(\"D MMM YYYY HH:mm:ss\")}\n\t\t\t\t\t\t\t\tstyle={{ cursor: \"help\" }}\n\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t{moment(lastUpdatedTime).format(\"D MMM YYYY\")}\n\t\t\t\t\t\t\t</span>{\" \"}\n\t\t\t\t\t\t\t/{\" \"}\n\t\t\t\t\t\t\t<span title={gitInfo.commit.hash} style={{ cursor: \"help\" }}>\n\t\t\t\t\t\t\t\t{gitInfo.commit.shortHash}\n\t\t\t\t\t\t\t</span>\n\t\t\t\t\t\t</NoSelect>\n\t\t\t\t\t</div>\n\t\t\t\t</Responsive>\n\t\t\t))}\n\n\t\t\t<Divider hidden />\n\t\t</Body>\n\t);\n}\n\nconst memoComponent = memo(SiteFooter, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as SiteFooter };\n";