export default "import { memo, useState } from \"react\";\nimport { Header, Icon } from \"semantic-ui-react\";\nimport styled from \"styled-components\";\n\nconst ContactItemWrapper = styled(({ hovered, ...props }) => <div {...props} />)`\n\t.contact-item {\n\t\ttransition: 250ms;\n\t\topacity: ${({ hovered }) => (hovered ? 1 : 0.75)};\n\t\t${({ hovered }) => (hovered ? \"transform: scale(1.05);\" : \"\")}\n\t}\n`;\n\nfunction ContactItem(props) {\n\tconst { href, icon, color, iconRotation, header, subHeader } = props;\n\tconst isUrl = new RegExp(\"^https?://\", \"i\").test(href);\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\treturn (\n\t\t// eslint-disable-next-line react/jsx-no-target-blank\n\t\t<a href={href} target={isUrl ? \"_blank\" : null} rel={isUrl ? \"external nofollow noopener noreferrer\" : null}>\n\t\t\t<ContactItemWrapper hovered={isHovered}>\n\t\t\t\t<Header\n\t\t\t\t\tclassName=\"contact-item\"\n\t\t\t\t\tas=\"h4\"\n\t\t\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\t\t\tonClick={() => setHovered(false)}\n\t\t\t\t>\n\t\t\t\t\t<Icon name={icon} color={color} rotated={iconRotation} />\n\t\t\t\t\t<Header.Content>\n\t\t\t\t\t\t{header}\n\t\t\t\t\t\t<Header.Subheader>{subHeader}</Header.Subheader>\n\t\t\t\t\t</Header.Content>\n\t\t\t\t</Header>\n\t\t\t</ContactItemWrapper>\n\t\t</a>\n\t);\n}\n\nconst memoComponent = memo(ContactItem, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as ContactItem };\n";