export default "import styled from \"styled-components\";\nimport ReactMarkdown from \"react-markdown\";\n\nconst FONT_SIZE_INCREMENT = 2;\nconst FONT_SIZE_MIN = 14;\n\nconst CustomMarkdown = styled(({ fontSizeLevel, ...props }) => <ReactMarkdown {...props} />)`\n\tword-wrap: break-word;\n\n\t.paragraph {\n\t\tfont-size: ${({ fontSizeLevel = 0 }) => fontSizeLevel * FONT_SIZE_INCREMENT + FONT_SIZE_MIN}px !important;\n\t\tline-height: 1.75em !important;\n\t\tmargin-bottom: 1em !important;\n\t}\n\n\tli {\n\t\tfont-size: ${({ fontSizeLevel = 0 }) => fontSizeLevel * FONT_SIZE_INCREMENT + FONT_SIZE_MIN}px !important;\n\t\tline-height: 1.75em !important;\n\t}\n\n\tpre {\n\t\toverflow: auto;\n\t\tborder-radius: 5px;\n\t\tpadding: 20px;\n\t\tcolor: #455a64;\n\t\tbackground-color: rgba(144, 164, 174, 0.2);\n\t\tborder: 1px solid #90a4ae;\n\t\tline-height: 1.2em;\n\t\ttransition: 250ms;\n\t\tfont-size: ${({ fontSizeLevel = 0 }) => fontSizeLevel * FONT_SIZE_INCREMENT + FONT_SIZE_MIN}px !important;\n\t}\n\n\t*:not(pre):not(code) > code {\n\t\tcolor: #d81b60;\n\t\tbackground-color: rgba(27, 31, 35, 0.05);\n\t\tborder-radius: 3px;\n\t\tpadding: 0.2em 0.4em;\n\t\ttransition: 250ms;\n\t}\n\n\tspan.react-syntax-highlighter-line-number {\n\t\t-webkit-touch-callout: none;\n\t\t-webkit-user-select: none;\n\t\t-khtml-user-select: none;\n\t\t-moz-user-select: none;\n\t\t-ms-user-select: none;\n\t\tuser-select: none;\n\t\t-webkit-user-drag: none;\n\t\t-khtml-user-drag: none;\n\t\t-moz-user-drag: none;\n\t\t-ms-user-drag: none;\n\t\tuser-drag: none;\n\t}\n\n\thr {\n\t\tmargin: 20px auto;\n\t\tborder: 1px solid rgba(144, 164, 174, 0.5);\n\t}\n\n\timg {\n\t\tposition: relative;\n\t\tmax-width: 50%;\n\t\tmax-height: 400px;\n\t\tdisplay: block;\n\t\tmargin: 20px auto;\n\t\tpadding: 3px;\n\t\tborder-radius: 15px;\n\t\tbox-shadow: 0 0 15px rgba(0, 0, 0, 0.5);\n\t\tbackground: linear-gradient(to right, red, orange, yellow);\n\t\tbackground-size: 200% 200%;\n\t\ttransition: 400ms cubic-bezier(0, 1.2, 0.2, 1.5);\n\t\tanimation: animated-border 3s ease infinite;\n\t\tcursor: zoom-in;\n\t}\n\n\t@keyframes animated-border {\n\t\t0% {\n\t\t\tbackground-position: 0% 0%;\n\t\t}\n\n\t\t50% {\n\t\t\tbackground-position: 100% 100%;\n\t\t}\n\n\t\t100% {\n\t\t\tbackground-position: 0% 0%;\n\t\t}\n\t}\n\n\tblockquote {\n\t\tfont-style: italic;\n\t\topacity: 0.5;\n\t\tpadding-left: 25px;\n\t\tborder-left: 5px solid rgba(41, 182, 246, 0.5);\n\t\ttransition: 250ms;\n\t}\n\n\ttable {\n\t\tborder-collapse: collapse;\n\t\tborder-style: hidden;\n\t\tborder-radius: 5px !important;\n\t\tbox-shadow: 0 0 0 1px #90a4ae;\n\t}\n\n\tthead th {\n\t\tbackground-color: rgba(144, 164, 174, 0.25);\n\t}\n\ttr:nth-child(even) td {\n\t\tbackground-color: rgba(144, 164, 174, 0.05);\n\t}\n\n\tth:first-child {\n\t\tborder-top-left-radius: 5px;\n\t}\n\tth:last-child {\n\t\tborder-top-right-radius: 5px;\n\t}\n\ttr:last-child td:first-child {\n\t\tborder-bottom-left-radius: 5px;\n\t}\n\ttr:last-child td:last-child {\n\t\tborder-bottom-right-radius: 5px;\n\t}\n\n\tth,\n\ttd {\n\t\tborder: 1px solid #90a4ae;\n\t\tpadding: 8px;\n\t\tline-height: 1.75em !important;\n\t\tfont-size: ${({ fontSizeLevel = 0 }) => fontSizeLevel * FONT_SIZE_INCREMENT + FONT_SIZE_MIN}px !important;\n\t}\n\n\t@media (hover: hover) {\n\t\tpre:hover {\n\t\t\tbackground-color: rgba(224, 224, 224, 0.4);\n\t\t}\n\n\t\t*:not(pre):not(code) > code:hover {\n\t\t\tbackground-color: rgba(236, 64, 122, 0.15);\n\t\t}\n\n\t\tblockquote:hover {\n\t\t\tborder-radius: 0 15px 15px 0;\n\t\t\tbackground-color: rgba(144, 164, 174, 0.25);\n\t\t}\n\t}\n`;\nexport default CustomMarkdown;\nexport { CustomMarkdown };\n";