export default "import { memo, useState } from \"react\";\nimport { useDispatch } from \"react-redux\";\nimport { changeImagePopupSource } from \"../../../../redux/action\";\n\nfunction Avatar(props) {\n\tconst [queryString, setQueryString] = useState(\"\");\n\n\tconst dispatch = useDispatch();\n\n\tconst src = \"avatar.jpg\";\n\n\treturn (\n\t\t<div style={{ paddingTop: \"3em\" }}>\n\t\t\t<picture>\n\t\t\t\t<source type=\"image/webp\" srcSet={encodeURIComponent(src.replace(/\\.(jpg|png|gif)/, \".webp\"))} />\n\t\t\t\t<img\n\t\t\t\t\tsrc={`${src}${queryString}`}\n\t\t\t\t\talt=\"My profile pic\"\n\t\t\t\t\tdraggable=\"false\"\n\t\t\t\t\tclassName=\"ui large circular centered spaced image\"\n\t\t\t\t\tonContextMenu={(e) => e.preventDefault()}\n\t\t\t\t\tonClick={() =>\n\t\t\t\t\t\tdispatch(\n\t\t\t\t\t\t\tchangeImagePopupSource({\n\t\t\t\t\t\t\t\timages: [src],\n\t\t\t\t\t\t\t\tactiveIndex: 0,\n\t\t\t\t\t\t\t})\n\t\t\t\t\t\t)\n\t\t\t\t\t}\n\t\t\t\t\tonError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}\n\t\t\t\t\tstyle={{\n\t\t\t\t\t\tcursor: \"pointer\",\n\t\t\t\t\t\tpadding: \"8px\",\n\t\t\t\t\t\tbackground: `\n\t\t\t\t\t\tradial-gradient(\n\t\t\t\t\t\t\tcircle at 50% 0,\n\t\t\t\t\t\t\trgba(255, 0, 0, 0.375),\n\t\t\t\t\t\t\trgba(255, 0, 0, 0) 70.71%\n\t\t\t\t\t\t),\n\t\t\t\t\t\tradial-gradient(\n\t\t\t\t\t\t\tcircle at 6.7% 75%,\n\t\t\t\t\t\t\trgba(0, 0, 255, 0.375),\n\t\t\t\t\t\t\trgba(0, 0, 255, 0) 70.71%\n\t\t\t\t\t\t),\n\t\t\t\t\t\tradial-gradient(\n\t\t\t\t\t\t\tcircle at 93.3% 75%,\n\t\t\t\t\t\t\trgba(0, 255, 0, 0.375),\n\t\t\t\t\t\t\trgba(0, 255, 0, 0) 70.71%\n\t\t\t\t\t\t)\n\t\t\t\t\t\twhite\n\t\t\t\t\t`,\n\t\t\t\t\t}}\n\t\t\t\t/>\n\t\t\t</picture>\n\t\t</div>\n\t);\n}\n\nconst memoComponent = memo(Avatar, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as Avatar };\n";