export default "import axios from \"axios\";\nimport { firebaseHost } from \"../config\";\n\nconst postComment = ({ firstName, lastName, phone, email, message, clientIp }) => {\n\tconst date = new Date();\n\n\treturn axios.post(`${firebaseHost}/comments.json`, {\n\t\t...(firstName && { firstName }),\n\t\t...(lastName && { lastName }),\n\t\t...(phone && { phone }),\n\t\t...(email && { email }),\n\t\t...(message && { message }),\n\t\t...(clientIp && { clientIp }),\n\t\tdate: date.getTime(),\n\t\tdateISO: date.toISOString(),\n\t});\n};\n\nconst exports = { postComment };\nexport default exports;\nexport { postComment };\n";