export default "import { memo } from \"react\";\nimport { Header } from \"semantic-ui-react\";\nimport gfm from \"remark-gfm\";\nimport { CustomMarkdown } from \"./CustomMarkdown\";\nimport {\n\tCustomParagraphRenderer,\n\tCustomHeadingRenderer,\n\tCustomLinkRenderer,\n\tCustomTableRenderer,\n\tCustomImageRenderer,\n} from \"./renderers\";\nimport { MarkdownTocUtils } from \"../../../../utils\";\n// import SyntaxHighlighter from \"react-syntax-highlighter\";\n// import { anOldHope } from \"react-syntax-highlighter/dist/esm/styles/hljs\";\n\n// const CustomCodeRenderer = ({ language, value }) => {\n// \treturn (\n// \t\t<SyntaxHighlighter\n// \t\t\tlanguage={language}\n// \t\t\tstyle={anOldHope}\n// \t\t\tshowLineNumbers={(value || \"\").includes(\"\\n\")}\n// \t\t\tlineNumberStyle={{}}\n// \t\t>\n// \t\t\t{value}\n// \t\t</SyntaxHighlighter>\n// \t);\n// };\n\nfunction MarkdownContent(props) {\n\tconst { markdown, fontSizeLevel } = props;\n\n\tconst processedMarkdown = markdown\n\t\t? markdown.replace(/\\t/g, \"    \").replace(/^<!-- [\\w-]*prettier[\\w-]* -->$/gm, \"\")\n\t\t: null;\n\n\tconst tocArray = markdown\n\t\t? MarkdownTocUtils.generateTocJsonFromMarkdown(processedMarkdown).map((toc) => ({\n\t\t\t\t...toc,\n\t\t\t\tcontent: toc.content.replace(/`/g, \"\"),\n\t\t  }))\n\t\t: [];\n\n\tconst headingRenderer = ({ level, children }) => (\n\t\t<CustomHeadingRenderer tocArray={tocArray} level={level}>\n\t\t\t{children}\n\t\t</CustomHeadingRenderer>\n\t);\n\n\treturn markdown ? (\n\t\t<div id=\"post-content\">\n\t\t\t<CustomMarkdown\n\t\t\t\tchildren={processedMarkdown}\n\t\t\t\tplugins={[gfm]}\n\t\t\t\tfontSizeLevel={fontSizeLevel}\n\t\t\t\tcomponents={{\n\t\t\t\t\tp: CustomParagraphRenderer,\n\t\t\t\t\th1: headingRenderer,\n\t\t\t\t\th2: headingRenderer,\n\t\t\t\t\th3: headingRenderer,\n\t\t\t\t\th4: headingRenderer,\n\t\t\t\t\th5: headingRenderer,\n\t\t\t\t\th6: headingRenderer,\n\t\t\t\t\ta: CustomLinkRenderer,\n\t\t\t\t\ttable: CustomTableRenderer,\n\t\t\t\t\timg: CustomImageRenderer,\n\t\t\t\t\t// code: CustomCodeRenderer,\n\t\t\t\t}}\n\t\t\t/>\n\t\t</div>\n\t) : (\n\t\t<Header as=\"h2\">Content not available.</Header>\n\t);\n}\nconst memoComponent = memo(\n\tMarkdownContent,\n\t(prev, next) => prev.markdown === next.markdown && prev.fontSizeLevel === next.fontSizeLevel\n);\nexport default memoComponent;\nexport { memoComponent as MarkdownContent };\n";