export default "import { memo, useState } from \"react\";\nimport { useHistory } from \"react-router-dom\";\nimport { useDispatch } from \"react-redux\";\nimport { Helmet } from \"react-helmet\";\nimport Lazyload from \"react-lazyload\";\nimport { Segment, Header, Label, Button, Divider, Icon, Responsive } from \"semantic-ui-react\";\nimport styled from \"styled-components\";\nimport { PageNotFound } from \"./PageNotFound\";\nimport { Body, NoSelect, GradientText } from \"../../Styles\";\nimport moment from \"moment\";\nimport { responsiveMaxWidth, responsiveMinWidth, workProjects, hobbyProjects } from \"../../../config\";\nimport { changeImagePopupSource } from \"../../../redux/action\";\nimport find from \"lodash/find\";\n\nconst CustomSegment = styled(Segment)`\n\tbackground-color: rgba(255, 255, 255, 0.25) !important;\n\n\timg {\n\t\tposition: relative;\n\t\tmax-width: 50%;\n\t\tmax-height: 400px;\n\t\tdisplay: block;\n\t\tmargin: 20px auto;\n\t\tborder-radius: 10px;\n\t\tbox-shadow: 0 0 15px rgba(0, 0, 0, 0.5);\n\t\tbackground-size: 200% 200%;\n\t\ttransition: transform 400ms cubic-bezier(0, 1.2, 0.2, 1.5);\n\t\tcursor: zoom-in;\n\t}\n`;\n\nfunction ProjectDetail(props) {\n\tconst { projectType, backPath } = props;\n\tconst projectId = props.match.params.projectId;\n\tconst project = find([...workProjects, ...hobbyProjects], { projectId });\n\tconst {\n\t\tprojectName,\n\t\tprojectNature,\n\t\tyear,\n\t\timages,\n\t\tposition,\n\t\tcontractPeriod,\n\t\tworkLocation,\n\t\tteamSize,\n\t\tdescriptionOverview,\n\t\tdescriptionContinued,\n\t\tdescription,\n\t} = project || {};\n\n\tconst colors = [\"#b71c1c\", \"#0d47a1\", \"#1b5e20\", \"#e65100\", \"#3e2723\"];\n\tconst yearColor = Number.isInteger(year) ? colors[(moment().year() % year) % colors.length] : \"#212121\";\n\n\tconst [hoverImageIndex, setHoverImageIndex] = useState(null);\n\tconst [queryString, setQueryString] = useState(\"\");\n\n\tconst dispatch = useDispatch();\n\n\tconst history = useHistory();\n\n\tconst backToAllProjectsButton = (\n\t\t<div\n\t\t\tonClick={() => {\n\t\t\t\tif (window.history?.length >= 2 && history.location?.state?.fromAllProjects) {\n\t\t\t\t\thistory.goBack();\n\t\t\t\t} else {\n\t\t\t\t\thistory.push(backPath);\n\t\t\t\t}\n\t\t\t}}\n\t\t>\n\t\t\t<Responsive\n\t\t\t\tas={Button}\n\t\t\t\tminWidth={responsiveMinWidth}\n\t\t\t\tbasic\n\t\t\t\tcompact\n\t\t\t\tcolor=\"pink\"\n\t\t\t\tsize=\"medium\"\n\t\t\t\tcontent={\n\t\t\t\t\t<>\n\t\t\t\t\t\tBack to <b>{projectType || \"\"}</b>\n\t\t\t\t\t</>\n\t\t\t\t}\n\t\t\t\ticon=\"arrow left\"\n\t\t\t\tlabelPosition=\"left\"\n\t\t\t/>\n\t\t\t<Responsive maxWidth={responsiveMaxWidth}>\n\t\t\t\t<Button basic compact color=\"pink\" size=\"medium\" icon=\"arrow left\" content=\"Back\" />\n\t\t\t</Responsive>\n\t\t</div>\n\t);\n\n\treturn (\n\t\t<Body>\n\t\t\t<Helmet>\n\t\t\t\t<title>\n\t\t\t\t\t{project ? projectName : \"404 Not Found\"} | {projectType || \"Project\"} |{\" \"}\n\t\t\t\t\t{process.env.REACT_APP_TITLE}\n\t\t\t\t</title>\n\t\t\t</Helmet>\n\n\t\t\t{project ? (\n\t\t\t\t<>\n\t\t\t\t\t{backToAllProjectsButton}\n\n\t\t\t\t\t<Header size=\"huge\" style={{ cursor: \"default\" }}>\n\t\t\t\t\t\t{projectName || \"\"}\n\t\t\t\t\t</Header>\n\t\t\t\t\t<Divider section />\n\n\t\t\t\t\t<CustomSegment style={{ padding: \"3em 5vw\" }}>\n\t\t\t\t\t\t<NoSelect>\n\t\t\t\t\t\t\t<Label.Group>\n\t\t\t\t\t\t\t\t<Label\n\t\t\t\t\t\t\t\t\ttag\n\t\t\t\t\t\t\t\t\tsize=\"large\"\n\t\t\t\t\t\t\t\t\tstyle={{\n\t\t\t\t\t\t\t\t\t\tcolor: \"white\",\n\t\t\t\t\t\t\t\t\t\tbackgroundColor: yearColor,\n\t\t\t\t\t\t\t\t\t}}\n\t\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t\t{Number.isInteger(year) ? \"Year \" + year : year || \"Others\"}\n\t\t\t\t\t\t\t\t</Label>\n\t\t\t\t\t\t\t\t<Label tag size=\"large\" color=\"teal\">\n\t\t\t\t\t\t\t\t\t{projectNature || \"\"}\n\t\t\t\t\t\t\t\t</Label>\n\t\t\t\t\t\t\t</Label.Group>\n\t\t\t\t\t\t</NoSelect>\n\n\t\t\t\t\t\t<GradientText style={{ paddingTop: \"2em\" }}>\n\t\t\t\t\t\t\t{position && (\n\t\t\t\t\t\t\t\t<>\n\t\t\t\t\t\t\t\t\tPosition: {position}\n\t\t\t\t\t\t\t\t\t<br />\n\t\t\t\t\t\t\t\t</>\n\t\t\t\t\t\t\t)}\n\t\t\t\t\t\t\t{contractPeriod && (\n\t\t\t\t\t\t\t\t<>\n\t\t\t\t\t\t\t\t\tContract period: {contractPeriod}\n\t\t\t\t\t\t\t\t\t<br />\n\t\t\t\t\t\t\t\t</>\n\t\t\t\t\t\t\t)}\n\t\t\t\t\t\t\t{workLocation && (\n\t\t\t\t\t\t\t\t<>\n\t\t\t\t\t\t\t\t\tWork location: {workLocation}\n\t\t\t\t\t\t\t\t\t<br />\n\t\t\t\t\t\t\t\t</>\n\t\t\t\t\t\t\t)}\n\t\t\t\t\t\t\t{teamSize && (\n\t\t\t\t\t\t\t\t<>\n\t\t\t\t\t\t\t\t\tTeam size: {teamSize}\n\t\t\t\t\t\t\t\t\t<br />\n\t\t\t\t\t\t\t\t</>\n\t\t\t\t\t\t\t)}\n\n\t\t\t\t\t\t\t{(position || contractPeriod || workLocation || teamSize) && <br />}\n\n\t\t\t\t\t\t\t{description || (\n\t\t\t\t\t\t\t\t\t<>\n\t\t\t\t\t\t\t\t\t\t{descriptionOverview}\n\t\t\t\t\t\t\t\t\t\t<br />\n\t\t\t\t\t\t\t\t\t\t<br />\n\t\t\t\t\t\t\t\t\t\t{descriptionContinued}\n\t\t\t\t\t\t\t\t\t</>\n\t\t\t\t\t\t\t\t) ||\n\t\t\t\t\t\t\t\t\"\"}\n\t\t\t\t\t\t</GradientText>\n\n\t\t\t\t\t\t{images && images.length > 0 && (\n\t\t\t\t\t\t\t<>\n\t\t\t\t\t\t\t\t<Divider horizontal section style={{ paddingBottom: \"2em\" }}>\n\t\t\t\t\t\t\t\t\t<Header as=\"h5\" style={{ opacity: 0.5 }}>\n\t\t\t\t\t\t\t\t\t\t<Icon name=\"photo\" />\n\t\t\t\t\t\t\t\t\t\tScreenshots / Photos\n\t\t\t\t\t\t\t\t\t</Header>\n\t\t\t\t\t\t\t\t</Divider>\n\n\t\t\t\t\t\t\t\t{images.map((image, i) => (\n\t\t\t\t\t\t\t\t\t<Lazyload key={i} once height=\"400px\" offset={400}>\n\t\t\t\t\t\t\t\t\t\t<picture>\n\t\t\t\t\t\t\t\t\t\t\t<source\n\t\t\t\t\t\t\t\t\t\t\t\ttype=\"image/webp\"\n\t\t\t\t\t\t\t\t\t\t\t\tsrcSet={encodeURIComponent(\n\t\t\t\t\t\t\t\t\t\t\t\t\timage.replace(/\\.(jpg|png|gif)/, \"-crop.webp\")\n\t\t\t\t\t\t\t\t\t\t\t\t)}\n\t\t\t\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t\t\t\t\t<img\n\t\t\t\t\t\t\t\t\t\t\t\tsrc={`${image}${queryString}`}\n\t\t\t\t\t\t\t\t\t\t\t\talt={image.replace(/\\.(jpg|png|gif)/, \"\")}\n\t\t\t\t\t\t\t\t\t\t\t\tdraggable={false}\n\t\t\t\t\t\t\t\t\t\t\t\tonContextMenu={(e) => e.preventDefault()}\n\t\t\t\t\t\t\t\t\t\t\t\tonMouseEnter={() => setHoverImageIndex(i)}\n\t\t\t\t\t\t\t\t\t\t\t\tonMouseOver={() => setHoverImageIndex(i)}\n\t\t\t\t\t\t\t\t\t\t\t\tonMouseLeave={() => setHoverImageIndex(null)}\n\t\t\t\t\t\t\t\t\t\t\t\tonTouchStart={() => setHoverImageIndex(i)}\n\t\t\t\t\t\t\t\t\t\t\t\tonTouchEnd={() => setHoverImageIndex(null)}\n\t\t\t\t\t\t\t\t\t\t\t\tonTouchCancel={() => setHoverImageIndex(null)}\n\t\t\t\t\t\t\t\t\t\t\t\tonClick={() => {\n\t\t\t\t\t\t\t\t\t\t\t\t\tsetHoverImageIndex(null);\n\t\t\t\t\t\t\t\t\t\t\t\t\tdispatch(\n\t\t\t\t\t\t\t\t\t\t\t\t\t\tchangeImagePopupSource({\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\timages,\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\tactiveIndex: i,\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t})\n\t\t\t\t\t\t\t\t\t\t\t\t\t);\n\t\t\t\t\t\t\t\t\t\t\t\t}}\n\t\t\t\t\t\t\t\t\t\t\t\tonError={() =>\n\t\t\t\t\t\t\t\t\t\t\t\t\tsetTimeout(() => setQueryString(`?${Math.random()}`), 1000)\n\t\t\t\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\t\t\t\tstyle={{\n\t\t\t\t\t\t\t\t\t\t\t\t\t...(i === hoverImageIndex && {\n\t\t\t\t\t\t\t\t\t\t\t\t\t\ttransform: \"scale(1.2)\",\n\t\t\t\t\t\t\t\t\t\t\t\t\t\tboxShadow: \"0 0 30px rgba(0, 0, 0, 1)\",\n\t\t\t\t\t\t\t\t\t\t\t\t\t\tzIndex: 1,\n\t\t\t\t\t\t\t\t\t\t\t\t\t}),\n\t\t\t\t\t\t\t\t\t\t\t\t\t...(hoverImageIndex === null ||\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t(i !== hoverImageIndex && {\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\tfilter: \"brightness(0.75) blur(2px)\",\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t})),\n\t\t\t\t\t\t\t\t\t\t\t\t}}\n\t\t\t\t\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t\t\t\t</picture>\n\t\t\t\t\t\t\t\t\t</Lazyload>\n\t\t\t\t\t\t\t\t))}\n\t\t\t\t\t\t\t</>\n\t\t\t\t\t\t)}\n\t\t\t\t\t</CustomSegment>\n\n\t\t\t\t\t<Divider section />\n\t\t\t\t\t{backToAllProjectsButton}\n\t\t\t\t</>\n\t\t\t) : (\n\t\t\t\t<PageNotFound />\n\t\t\t)}\n\t\t</Body>\n\t);\n}\n\nconst memoComponent = memo(ProjectDetail, (prev, next) => prev.match.params.projectId === next.match.params.projectId);\nexport default memoComponent;\nexport { memoComponent as ProjectDetail };\n";