export default "import { memo, useState } from \"react\";\nimport { useDispatch } from \"react-redux\";\nimport Lazyload from \"react-lazyload\";\nimport { useSwipeable } from \"react-swipeable\";\nimport styled, { keyframes } from \"styled-components\";\nimport { NoSelect } from \"../Styles\";\nimport { LoadingIndicator } from \"./LoadingIndicator\";\nimport { changeImagePopupSource } from \"../../redux/action\";\n\nconst scrollImageHorizontally = (hiddenWidth) => keyframes`\n\t98% {\n\t\ttransform: translateX(-${hiddenWidth}px);\n\t}\n\t100% {\n\t\ttransform: translateX(0px);\n\t}\n`;\n\nconst scrollImageVertically = (hiddenHeight) => keyframes`\n\t 98% {\n\t \ttransform: translateY(-${hiddenHeight}px);\n\t }\n\t 100% {\n\t \ttransform: translateY(0px);\n\t }\n`;\n\nconst GalleryWrapper = styled.div`\n\tdisplay: inline-block;\n\tposition: relative;\n\tmax-width: ${({ maxWidth }) => maxWidth};\n\tmax-height: ${({ maxHeight }) => maxHeight};\n\tmargin: 15px;\n\tborder-radius: 20px;\n\toverflow: hidden;\n\ttransition: 250ms;\n\tcursor: zoom-in;\n\n\t* {\n\t\ttransition: 250ms;\n\t}\n\n\timg {\n\t\tobject-fit: cover;\n\t\tanimation: ${({ hiddenWidth, hiddenHeight }) =>\n\t\t\t\thiddenWidth\n\t\t\t\t\t? scrollImageHorizontally(hiddenWidth)\n\t\t\t\t\t: hiddenHeight\n\t\t\t\t\t? scrollImageVertically(hiddenHeight)\n\t\t\t\t\t: \"none\"}\n\t\t\t15s linear infinite;\n\t\t${({ loaded }) => (loaded ? \"\" : \"animation: loading-image 2.5s linear infinite;\")};\n\t}\n\n\t@keyframes loading-image {\n\t\t50% {\n\t\t\topacity: 0.5;\n\t\t\tfilter: brightness(200%);\n\t\t}\n\t}\n`;\n\nconst GalleryButtons = styled.div`\n\t.image-button {\n\t\tposition: absolute;\n\t\ttop: 0;\n\t\twidth: 33%;\n\t\theight: 100%;\n\t\tborder: none;\n\t\ttransition: 500ms;\n\t\tbackground-color: transparent;\n\t\tcursor: ${({ loaded }) => (loaded ? \"pointer\" : \"wait\")};\n\t\topacity: ${({ loaded }) => (loaded ? 0.75 : 0.25)};\n\t}\n\n\t.previous-image-button {\n\t\tleft: 0;\n\t\tborder-radius: 20px 0 0 20px;\n\t}\n\n\t.next-image-button {\n\t\tright: 0;\n\t\tborder-radius: 0 20px 20px 0;\n\t}\n`;\n\nconst IndexAndCount = styled.span`\n\tposition: absolute;\n\ttop: 10px;\n\tright: 10px;\n\tpadding: 3px 10px 3px 10px;\n\tcolor: white;\n\tbackground-color: rgba(0, 0, 0, 0.8);\n\tborder-radius: 15px;\n\ttext-align: center;\n`;\n\nfunction Gallery(props) {\n\tconst { images } = props;\n\tlet { width, height } = props;\n\tif (!width && !height) {\n\t\twidth = \"300px\";\n\t\theight = \"300px\";\n\t}\n\twidth = (width || \"auto\") + \"\";\n\theight = (height || \"auto\") + \"\";\n\n\tconst [displayIndex, setDisplayIndex] = useState(null);\n\tconst [activeIndex, setActiveIndex] = useState(0);\n\tconst [isLoaded, setLoaded] = useState(false);\n\tconst [queryString, setQueryString] = useState(\"\");\n\tconst [preferredWidth, setPreferredWidth] = useState(width);\n\tconst [hiddenWidth, setHiddenWidth] = useState(0);\n\tconst [hiddenHeight, setHiddenHeight] = useState(0);\n\tconst [originalWidth, setOriginalWidth] = useState(undefined);\n\n\tconst dispatch = useDispatch();\n\n\tconst swipeConfig = {\n\t\tonSwipedLeft: () => {\n\t\t\tif (!images || images.length < 2 || !isLoaded) {\n\t\t\t\treturn;\n\t\t\t}\n\t\t\tsetLoaded(false);\n\t\t\tsetQueryString(\"\");\n\t\t\tsetActiveIndex((activeIndex + 1) % images.length);\n\t\t},\n\t\tonSwipedRight: () => {\n\t\t\tif (!images || images.length < 2 || !isLoaded) {\n\t\t\t\treturn;\n\t\t\t}\n\t\t\tsetLoaded(false);\n\t\t\tsetQueryString(\"\");\n\t\t\tsetActiveIndex((activeIndex + images.length - 1) % images.length);\n\t\t},\n\t\tpreventDefaultTouchmoveEvent: true,\n\t};\n\tconst imageCenterSwipeHandlers = useSwipeable(swipeConfig);\n\tconst buttonsSwipeHandlers = useSwipeable(swipeConfig);\n\n\tconst GalleryButton = (props) => {\n\t\tconst { className, clickToIndex } = props;\n\n\t\tconst [isHovered, setHovered] = useState(false);\n\n\t\treturn (\n\t\t\t<button\n\t\t\t\tclassName={className}\n\t\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\t\tonClick={(e) => {\n\t\t\t\t\te.preventDefault();\n\t\t\t\t\te.stopPropagation();\n\n\t\t\t\t\tsetHovered(false);\n\n\t\t\t\t\tif (!isLoaded) {\n\t\t\t\t\t\treturn;\n\t\t\t\t\t}\n\t\t\t\t\tsetLoaded(false);\n\t\t\t\t\tsetQueryString(\"\");\n\t\t\t\t\tsetActiveIndex(clickToIndex);\n\t\t\t\t}}\n\t\t\t\tstyle={{ ...(isHovered && { backgroundColor: \"rgba(3, 169, 244, 0.25)\" }) }}\n\t\t\t/>\n\t\t);\n\t};\n\n\treturn (\n\t\t<>\n\t\t\t{images && images.length > 0 && (\n\t\t\t\t<Lazyload once height={height} offset={400}>\n\t\t\t\t\t<GalleryWrapper\n\t\t\t\t\t\tloaded={isLoaded}\n\t\t\t\t\t\tmaxWidth={width}\n\t\t\t\t\t\tmaxHeight={height}\n\t\t\t\t\t\thiddenWidth={hiddenWidth}\n\t\t\t\t\t\thiddenHeight={hiddenHeight}\n\t\t\t\t\t\tonClick={() =>\n\t\t\t\t\t\t\tdispatch(\n\t\t\t\t\t\t\t\tchangeImagePopupSource({\n\t\t\t\t\t\t\t\t\timages,\n\t\t\t\t\t\t\t\t\tactiveIndex: activeIndex,\n\t\t\t\t\t\t\t\t})\n\t\t\t\t\t\t\t)\n\t\t\t\t\t\t}\n\t\t\t\t\t\t{...imageCenterSwipeHandlers}\n\t\t\t\t\t>\n\t\t\t\t\t\t<picture>\n\t\t\t\t\t\t\t<source\n\t\t\t\t\t\t\t\ttype=\"image/webp\"\n\t\t\t\t\t\t\t\tsrcSet={encodeURIComponent(\n\t\t\t\t\t\t\t\t\timages[activeIndex].replace(/\\.(jpg|png|gif)/, \"-crop.webp\")\n\t\t\t\t\t\t\t\t)}\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t<img\n\t\t\t\t\t\t\t\tsrc={`${images[activeIndex]}${queryString}`}\n\t\t\t\t\t\t\t\talt={images[activeIndex].replace(/\\.(jpg|png|gif)/, \"\")}\n\t\t\t\t\t\t\t\tdraggable={false}\n\t\t\t\t\t\t\t\twidth={preferredWidth}\n\t\t\t\t\t\t\t\tonContextMenu={(e) => e.preventDefault()}\n\t\t\t\t\t\t\t\tonLoad={({ target }) => {\n\t\t\t\t\t\t\t\t\tsetLoaded(true);\n\t\t\t\t\t\t\t\t\tsetDisplayIndex(activeIndex);\n\n\t\t\t\t\t\t\t\t\tlet windowWidth = originalWidth;\n\t\t\t\t\t\t\t\t\tconst windowHeight = Number(height.replace(/px/, \"\"));\n\t\t\t\t\t\t\t\t\tconst imgWidth = target.width;\n\t\t\t\t\t\t\t\t\tconst imgHeight = target.height;\n\t\t\t\t\t\t\t\t\tlet hiddenWidth = 0;\n\t\t\t\t\t\t\t\t\tlet hiddenHeight = 0;\n\n\t\t\t\t\t\t\t\t\tif (!windowWidth) {\n\t\t\t\t\t\t\t\t\t\tsetOriginalWidth(imgWidth);\n\t\t\t\t\t\t\t\t\t\twindowWidth = imgWidth;\n\t\t\t\t\t\t\t\t\t}\n\n\t\t\t\t\t\t\t\t\tif (imgWidth / imgHeight > windowWidth / windowHeight) {\n\t\t\t\t\t\t\t\t\t\tconst newScaleWidth = (windowHeight * imgWidth) / imgHeight / windowWidth;\n\t\t\t\t\t\t\t\t\t\tsetPreferredWidth(newScaleWidth * 100 + \"%\");\n\n\t\t\t\t\t\t\t\t\t\thiddenWidth = (windowHeight * imgWidth) / imgHeight - windowWidth;\n\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\tif (imgHeight / imgWidth > windowHeight / windowWidth) {\n\t\t\t\t\t\t\t\t\t\tsetPreferredWidth(\"100%\");\n\t\t\t\t\t\t\t\t\t\thiddenHeight = (windowWidth * imgHeight) / imgWidth - windowHeight;\n\t\t\t\t\t\t\t\t\t}\n\n\t\t\t\t\t\t\t\t\tsetHiddenWidth(hiddenWidth);\n\n\t\t\t\t\t\t\t\t\tif (imgWidth < windowWidth) {\n\t\t\t\t\t\t\t\t\t\t// const newImg = new Image();\n\t\t\t\t\t\t\t\t\t\t// newImg.onload = ({ target }) => {\n\t\t\t\t\t\t\t\t\t\t// \tsetHiddenHeight(target.height - windowHeight);\n\t\t\t\t\t\t\t\t\t\t// };\n\t\t\t\t\t\t\t\t\t\t// newImg.src = target.src;\n\t\t\t\t\t\t\t\t\t\tsetHiddenHeight(0);\n\t\t\t\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\t\t\t\tsetHiddenHeight(hiddenHeight);\n\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t}}\n\t\t\t\t\t\t\t\tonError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t</picture>\n\t\t\t\t\t</GalleryWrapper>\n\n\t\t\t\t\t{images.length >= 2 && (\n\t\t\t\t\t\t<GalleryButtons loaded={isLoaded} {...buttonsSwipeHandlers}>\n\t\t\t\t\t\t\t<GalleryButton\n\t\t\t\t\t\t\t\tclassName=\"image-button previous-image-button\"\n\t\t\t\t\t\t\t\tclickToIndex={(activeIndex + images.length - 1) % images.length}\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t\t<GalleryButton\n\t\t\t\t\t\t\t\tclassName=\"image-button next-image-button\"\n\t\t\t\t\t\t\t\tclickToIndex={(activeIndex + 1) % images.length}\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t</GalleryButtons>\n\t\t\t\t\t)}\n\n\t\t\t\t\t{images.length > 1 && (\n\t\t\t\t\t\t<NoSelect style={{ pointerEvents: \"none\" }}>\n\t\t\t\t\t\t\t<IndexAndCount>\n\t\t\t\t\t\t\t\t{displayIndex + 1}/{images.length}\n\t\t\t\t\t\t\t</IndexAndCount>\n\t\t\t\t\t\t</NoSelect>\n\t\t\t\t\t)}\n\n\t\t\t\t\t{!isLoaded && <LoadingIndicator />}\n\t\t\t\t</Lazyload>\n\t\t\t)}\n\t\t</>\n\t);\n}\n\nconst memoComponent = memo(Gallery, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as Gallery };\n";