export default "import { memo, Fragment } from \"react\";\nimport { Helmet } from \"react-helmet\";\nimport { Header, Divider } from \"semantic-ui-react\";\nimport { Body } from \"../../Styles\";\nimport { Project } from \"../Project\";\nimport { hobbyProjects } from \"../../../config\";\n\nfunction HobbyProject(props) {\n\treturn (\n\t\t<Body>\n\t\t\t<Helmet>\n\t\t\t\t<title>My Hobby Projects | {process.env.REACT_APP_TITLE}</title>\n\t\t\t</Helmet>\n\n\t\t\t<Header size=\"huge\" style={{ cursor: \"default\" }}>\n\t\t\t\tMy Hobby Projects\n\t\t\t</Header>\n\t\t\t{(hobbyProjects || [])\n\t\t\t\t.map(({ projectId, ...rest }) => ({ ...rest, path: `/hobbyProject/${projectId}` }))\n\t\t\t\t.map((project, i, projects) => (\n\t\t\t\t\t<Fragment key={i}>\n\t\t\t\t\t\t<Divider section />\n\t\t\t\t\t\t<Project detail={project} inverted={(projects.length - i) % 2 === 1} />\n\t\t\t\t\t</Fragment>\n\t\t\t\t))}\n\t\t</Body>\n\t);\n}\n\nconst memoComponent = memo(HobbyProject, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as HobbyProject };\n";