export default "import { memo, useState } from \"react\";\nimport { Link, useLocation } from \"react-router-dom\";\nimport { Container, Menu, Dropdown, Responsive } from \"semantic-ui-react\";\nimport { NameAndRole, Name, Role } from \"./Styles\";\nimport { Body } from \"./../Styles\";\nimport { responsiveMinWidth } from \"../../config\";\n\nfunction SiteHeader(props) {\n\tconst [openExp, setOpenExp] = useState(false);\n\n\tconst location = useLocation();\n\tconst currentPath = location.pathname;\n\n\tconst linkPath = (to) => ({\n\t\t...(currentPath === to ? {} : { as: Link, to }),\n\t\tactive: currentPath === to,\n\t\tstyle: {\n\t\t\ttextAlign: \"center\",\n\t\t\tpointerEvents: currentPath === to && \"none\",\n\t\t},\n\t});\n\n\treturn (\n\t\t<>\n\t\t\t<NameAndRole>\n\t\t\t\t<Name>Chung Cheuk Hang Michael</Name>\n\t\t\t\t<Role>Web Developer</Role>\n\t\t\t</NameAndRole>\n\n\t\t\t<Responsive as={Body} minWidth={responsiveMinWidth} style={{ paddingBottom: \"1em\" }}>\n\t\t\t\t<Container text textAlign=\"center\">\n\t\t\t\t\t<Menu stackable borderless compact widths={5} size=\"tiny\" inverted>\n\t\t\t\t\t\t<Menu.Item name=\"Home\" color=\"blue\" {...linkPath(\"/\")} draggable={false} />\n\t\t\t\t\t\t<Dropdown\n\t\t\t\t\t\t\titem\n\t\t\t\t\t\t\tsimple\n\t\t\t\t\t\t\ttext=\"My Experiences\"\n\t\t\t\t\t\t\topen={openExp}\n\t\t\t\t\t\t\tonMouseEnter={() => setOpenExp(true)}\n\t\t\t\t\t\t\tonMouseLeave={() => setOpenExp(false)}\n\t\t\t\t\t\t>\n\t\t\t\t\t\t\t<Dropdown.Menu>\n\t\t\t\t\t\t\t\t<Dropdown.Item {...linkPath(\"/workExp\")} draggable={false}>\n\t\t\t\t\t\t\t\t\tWork\n\t\t\t\t\t\t\t\t</Dropdown.Item>\n\t\t\t\t\t\t\t\t<Dropdown.Item {...linkPath(\"/hobbyProject\")} draggable={false}>\n\t\t\t\t\t\t\t\t\tHobby Projects\n\t\t\t\t\t\t\t\t</Dropdown.Item>\n\t\t\t\t\t\t\t</Dropdown.Menu>\n\t\t\t\t\t\t</Dropdown>\n\n\t\t\t\t\t\t<Menu.Item name=\"Personality\" color=\"purple\" {...linkPath(\"/personality\")} draggable={false} />\n\t\t\t\t\t\t<Menu.Item name=\"Blog\" color=\"teal\" {...linkPath(\"/blog\")} draggable={false} />\n\t\t\t\t\t\t<Menu.Item name=\"Contact\" color=\"yellow\" {...linkPath(\"/contact\")} draggable={false} />\n\t\t\t\t\t</Menu>\n\t\t\t\t</Container>\n\t\t\t</Responsive>\n\t\t</>\n\t);\n}\n\nconst memoComponent = memo(SiteHeader, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as SiteHeader };\n";