export default "import { memo } from \"react\";\nimport styled from \"styled-components\";\nimport { MenuItem } from \"./MenuItem\";\n\nconst PATHS = [\n\t{\n\t\tpath: \"/\",\n\t\ttitle: \"Home\",\n\t},\n\t{\n\t\tpath: \"/workExp\",\n\t\ttitle: \"Work Experiences\",\n\t},\n\t{\n\t\tpath: \"/hobbyProject\",\n\t\ttitle: \"Hobby Projects\",\n\t},\n\t{\n\t\tpath: \"/personality\",\n\t\ttitle: \"Personality\",\n\t},\n\t{\n\t\tpath: \"/blog\",\n\t\ttitle: \"Blog\",\n\t},\n\t{\n\t\tpath: \"/contact\",\n\t\ttitle: \"Contact\",\n\t},\n];\n\nconst Overlay = styled(({ navMenuOpen, ...props }) => <div {...props} />)`\n\tposition: fixed;\n\ttop: 0;\n\tleft: 0;\n\twidth: 100%;\n\theight: 100%;\n\ttransition: 250ms;\n\toverflow: hidden;\n\tz-index: 1000;\n\n\topacity: ${({ navMenuOpen }) => (navMenuOpen ? 1 : 0)};\n\tvisibility: ${({ navMenuOpen }) => (navMenuOpen ? \"visible\" : \"hidden\")};\n\n\tli {\n\t\tanimation: ${({ navMenuOpen }) => (navMenuOpen ? \"fadeInRight 0.5s ease forwards\" : \"\")};\n\t\tanimation-delay: ${({ navMenuOpen }) => (navMenuOpen ? \"0.1s\" : \"\")};\n\n\t\t&:nth-of-type(2) {\n\t\t\tanimation-delay: ${({ navMenuOpen }) => (navMenuOpen ? \"0.2s\" : \"\")};\n\t\t}\n\t\t&:nth-of-type(3) {\n\t\t\tanimation-delay: ${({ navMenuOpen }) => (navMenuOpen ? \"0.3s\" : \"\")};\n\t\t}\n\t\t&:nth-of-type(4) {\n\t\t\tanimation-delay: ${({ navMenuOpen }) => (navMenuOpen ? \"0.4s\" : \"\")};\n\t\t}\n\t\t&:nth-of-type(5) {\n\t\t\tanimation-delay: ${({ navMenuOpen }) => (navMenuOpen ? \"0.5s\" : \"\")};\n\t\t}\n\t\t&:nth-of-type(6) {\n\t\t\tanimation-delay: ${({ navMenuOpen }) => (navMenuOpen ? \"0.6s\" : \"\")};\n\t\t}\n\t}\n\n\tnav {\n\t\tposition: relative;\n\t\theight: 70%;\n\t\ttop: 50%;\n\t\ttransform: translateY(-50%);\n\t\tfont-size: 30px;\n\t\tfont-weight: bold;\n\t\ttext-align: center;\n\t}\n\n\tul {\n\t\tlist-style: none;\n\t\tpadding: 0;\n\t\tmargin: 0 auto;\n\t\tdisplay: inline-block;\n\t\tposition: relative;\n\t\theight: 100%;\n\n\t\tli {\n\t\t\tdisplay: block;\n\t\t\theight: 25%;\n\t\t\theight: calc(100% / ${PATHS.length});\n\t\t\tmin-height: 50px;\n\t\t\tposition: relative;\n\t\t\topacity: 0;\n\t\t}\n\t}\n`;\n\nfunction FullScreenMenu(props) {\n\tconst { navMenuOpen, menuItemCallback } = props;\n\n\treturn (\n\t\t<Overlay navMenuOpen={navMenuOpen}>\n\t\t\t<nav>\n\t\t\t\t<ul>\n\t\t\t\t\t{PATHS.map((path, i) => (\n\t\t\t\t\t\t<li key={i}>\n\t\t\t\t\t\t\t<MenuItem\n\t\t\t\t\t\t\t\ttitle={path.title}\n\t\t\t\t\t\t\t\tpath={path.path}\n\t\t\t\t\t\t\t\tmenuItemCallback={menuItemCallback}\n\t\t\t\t\t\t\t></MenuItem>\n\t\t\t\t\t\t</li>\n\t\t\t\t\t))}\n\t\t\t\t</ul>\n\t\t\t</nav>\n\t\t</Overlay>\n\t);\n}\n\nconst memoComponent = memo(FullScreenMenu, (prev, next) => prev.navMenuOpen === next.navMenuOpen);\nexport default memoComponent;\nexport { memoComponent as FullScreenMenu };\n";