export default "import { memo } from \"react\";\nimport styled from \"styled-components\";\n\nfunction getCubeStyle(index, itemCountPerRow) {\n\treturn `\n\t\t.sk-cube${index + 1} {\n\t\t\tanimation-delay: ${Number(0.1 * (Math.floor(index / 3) + (index % itemCountPerRow))).toFixed(1)}s;\n\t\t}\n\t`;\n}\n\nconst LoadingIndicatorWrapper = styled.div`\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-items: center;\n\n\tposition: absolute;\n\ttop: 0;\n\tleft: 0;\n\twidth: 100%;\n\theight: 100%;\n\tpointer-events: none;\n`;\n\nconst LoadingIndicatorContainer = styled.div`\n\tposition: absolute;\n\twidth: 40px;\n\theight: 40px;\n\n\t.sk-cube {\n\t\twidth: 33%;\n\t\theight: 33%;\n\t\tbackground-color: rgba(255, 87, 34, 0.75);\n\t\tfloat: left;\n\t\ttransform: scale3d(0, 0, 1);\n\t\tanimation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;\n\t}\n\n\t${Array(9)\n\t\t.fill(null)\n\t\t.map((v, i) => getCubeStyle(i, 3))\n\t\t.join(\"\")}\n\n\t@keyframes sk-cubeGridScaleDelay {\n\t\t0%,\n\t\t100% {\n\t\t\ttransform: scale3d(0, 0, 1);\n\t\t}\n\t\t35%,\n\t\t70% {\n\t\t\ttransform: scale3d(1, 1, 1);\n\t\t}\n\t}\n`;\n\nfunction LoadingIndicator(props) {\n\treturn (\n\t\t<LoadingIndicatorWrapper>\n\t\t\t<LoadingIndicatorContainer>\n\t\t\t\t<div className=\"sk-cube sk-cube1\"></div>\n\t\t\t\t<div className=\"sk-cube sk-cube2\"></div>\n\t\t\t\t<div className=\"sk-cube sk-cube3\"></div>\n\t\t\t\t<div className=\"sk-cube sk-cube4\"></div>\n\t\t\t\t<div className=\"sk-cube sk-cube5\"></div>\n\t\t\t\t<div className=\"sk-cube sk-cube6\"></div>\n\t\t\t\t<div className=\"sk-cube sk-cube7\"></div>\n\t\t\t\t<div className=\"sk-cube sk-cube8\"></div>\n\t\t\t\t<div className=\"sk-cube sk-cube9\"></div>\n\t\t\t</LoadingIndicatorContainer>\n\t\t</LoadingIndicatorWrapper>\n\t);\n}\n\nconst memoComponent = memo(LoadingIndicator, (prev, next) => true);\nexport default memoComponent;\nexport { memoComponent as LoadingIndicator };\n";