export default "import { memo } from \"react\";\nimport \"./index.css\";\n\nfunction ReadingProgressBar(props) {\n\tconst { progress } = props;\n\n\treturn <progress id=\"reading-progress-bar\" max=\"100\" value={progress * 100}></progress>;\n}\n\nconst memoComponent = memo(ReadingProgressBar, (prev, next) => prev.progress === next.progress);\nexport default memoComponent;\nexport { memoComponent as ReadingProgressBar };\n";