export default "import { memo, useState } from \"react\";\nimport { Link, useLocation } from \"react-router-dom\";\nimport styled from \"styled-components\";\n\nconst MenuItemWrapper = styled(({ hovered, ...props }) => <span {...props} />)`\n\ta {\n\t\tposition: relative;\n\t\tcolor: #e91e63;\n\t\ttext-decoration: none;\n\t\toverflow: hidden;\n\n\t\t-webkit-touch-callout: none;\n\t\t-webkit-user-select: none;\n\t\t-khtml-user-select: none;\n\t\t-moz-user-select: none;\n\t\t-ms-user-select: none;\n\t\tuser-select: none;\n\t\t-webkit-user-drag: none;\n\t\t-khtml-user-drag: none;\n\t\t-moz-user-drag: none;\n\t\t-ms-user-drag: none;\n\t\tuser-drag: none;\n\n\t\t&::after {\n\t\t\tcontent: \"\";\n\t\t\tposition: absolute;\n\t\t\tbottom: 0;\n\t\t\tleft: 50%;\n\t\t\twidth: ${({ hovered }) => (hovered ? 100 : 0)}%;\n\t\t\ttransform: translateX(-50%);\n\t\t\theight: 3px;\n\t\t\tbackground: #e91e63;\n\t\t\ttransition: 250ms;\n\t\t}\n\t}\n`;\n\nfunction MenuItem(props) {\n\tconst { title, path, menuItemCallback } = props;\n\n\tconst [isHovered, setHovered] = useState(false);\n\n\tconst { pathname } = useLocation();\n\n\treturn (\n\t\t<MenuItemWrapper hovered={isHovered}>\n\t\t\t<Link\n\t\t\t\tclassName=\"code\"\n\t\t\t\tto={path}\n\t\t\t\tonMouseEnter={() => setHovered(true)}\n\t\t\t\tonMouseOver={() => setHovered(true)}\n\t\t\t\tonMouseLeave={() => setHovered(false)}\n\t\t\t\tonTouchStart={() => setHovered(true)}\n\t\t\t\tonTouchEnd={() => setHovered(false)}\n\t\t\t\tonTouchCancel={() => setHovered(false)}\n\t\t\t\tonClick={() => {\n\t\t\t\t\tsetHovered(false);\n\t\t\t\t\tif (pathname !== path && menuItemCallback) {\n\t\t\t\t\t\tmenuItemCallback();\n\t\t\t\t\t}\n\t\t\t\t}}\n\t\t\t\tstyle={{\n\t\t\t\t\t...(pathname === path && {\n\t\t\t\t\t\topacity: 0.25,\n\t\t\t\t\t\tpointerEvents: \"none\",\n\t\t\t\t\t}),\n\t\t\t\t}}\n\t\t\t>\n\t\t\t\t{title}\n\t\t\t</Link>\n\t\t</MenuItemWrapper>\n\t);\n}\n\nconst memoComponent = memo(\n\tMenuItem,\n\t(prev, next) =>\n\t\tprev.title === next.title && prev.path === next.path && prev.menuItemCallback === next.menuItemCallback\n);\nexport default memoComponent;\nexport { memoComponent as MenuItem };\n";